import { useEffect, useRef, useState } from 'react';
import { generateData } from '../../utils/mock';

const ROW_HEIGHT = 40;
const BUFFER_SIZE = 20;

export const FixedHeightVisualList = () => {
  const [list] = useState(generateData(10000));
  const containerRef = useRef<HTMLDivElement>(null);
  const contentRef = useRef<HTMLDivElement>(null);
  const lastScrollTop = useRef<number>(0);
  const [range, setRange] = useState([
    0,
    Math.min(Math.ceil(window.innerHeight / ROW_HEIGHT), list.length),
  ]);
  const rangeStartRef = useRef(0);
  rangeStartRef.current = range[0];

  useEffect(() => {
    const container = containerRef.current;
    const listNode = contentRef.current;
    if (!container || !listNode) return;

    const handleScroll = () => {
      const containerRect = container.getBoundingClientRect();
      const listRect = listNode.getBoundingClientRect();
      const visibleLength = Math.ceil(container.clientHeight / ROW_HEIGHT);
      const scrollTop = containerRect.top - listRect.top;

      let dir = 'bottom';
      if (scrollTop < lastScrollTop.current) {
        dir = 'top';
      }
      lastScrollTop.current = scrollTop;

      const hiddenStartIndex = Math.floor(scrollTop / ROW_HEIGHT);
      let startIndex = 0;
      let endIndex = 0;

      if (dir === 'bottom') {
        startIndex = hiddenStartIndex;
        endIndex = hiddenStartIndex + visibleLength + BUFFER_SIZE;
      } else {
        startIndex = Math.max(hiddenStartIndex - BUFFER_SIZE, 0);
        endIndex = hiddenStartIndex + visibleLength;
      }

      if (rangeStartRef.current === startIndex) return;
      setRange([startIndex, endIndex]);
    };

    container.addEventListener('scroll', handleScroll);
    return () => {
      container.removeEventListener('scroll', handleScroll);
    };
  }, []);

  const rangeStart = range[0];
  const rangeEnd = range[1];
  return (
    <div className='relative h-[800px] w-96 overflow-y-scroll box-border border' ref={containerRef}>
      <div style={{ height: `${list.length * ROW_HEIGHT}px` }} ref={contentRef}>
        {list.slice(rangeStart, rangeEnd).map((item, index) => {
          return (
            <div
              key={item.id}
              className='absolute w-full border-b-[1px]'
              style={{ top: `${(rangeStart + index) * ROW_HEIGHT}px`, height: ROW_HEIGHT }}
            >
              <div className='flex items-center h-full px-4'>{item.index}</div>
            </div>
          );
        })}
      </div>
    </div>
  );
};
