import { clamp, sum, throttle } from 'lodash-es';
import React, { createContext, FC, useContext, useMemo, useRef, useState } from 'react';
import { createPortal } from 'react-dom';
import { BsArrowDownRight } from 'react-icons/bs';
import { v4 } from 'uuid';
import { cx } from '../utils/classnames';
import { getIntersectionRatio } from '../utils/intersection-ratio';

const MaxColWidth = 400;
const MinColWidth = 80;
const MaxRowHeight = 400;
const MinRowHeight = 80;
const DefaultCellWidth = 80;
const DefaultCellHeight = 80;
const AreaPrecisionOffset = 100;

interface Point {
  x: number;
  y: number;
}
interface CellData {
  content: string;
  colSpan: number;
  rowSpan: number;
  isMerged: boolean;
  colId: string;
  rowId: string;
  id: string;
}

interface TableCol {
  width: number;
  id: string;
}

interface TableRow {
  height: number;
  id: string;
}

interface TableData {
  tableCol: TableCol[];
  tableRow: TableRow[];
  tableContent: CellData[][];
}

interface SelectedCellContext {
  selectedCell: string[];
  setSelectedCell: React.Dispatch<React.SetStateAction<string[]>>;
}
const SelectedCellContext = createContext<SelectedCellContext | undefined>(undefined);
const useSelectedCell = () => {
  const context = useContext(SelectedCellContext);
  if (!context) {
    throw new Error('SelectedCellContext 不存在');
  }

  return context;
};

interface TableDataContext {
  tableData: TableData;
  setTableData: React.Dispatch<React.SetStateAction<TableData>>;
  tableRef: React.MutableRefObject<HTMLTableElement | null>;
}
const TableDataContext = createContext<TableDataContext | undefined>(undefined);
const useTableData = () => {
  const context = useContext(TableDataContext);
  if (!context) {
    throw new Error('TableDataContext 不存在');
  }

  return context;
};

export const SimpleTable = () => {
  const [selectedCell, setSelectedCell] = useState<string[]>([]);
  const [tableData, setTableData] = useState(initTable());
  const tableRef = useRef<HTMLTableElement | null>(null);

  const selectedCellContextValue = useMemo(() => {
    return {
      selectedCell,
      setSelectedCell,
    };
  }, [selectedCell]);

  const tableDataContextValue = useMemo(() => {
    return {
      tableData,
      setTableData,
      tableRef,
    };
  }, [tableData]);

  return (
    <SelectedCellContext.Provider value={selectedCellContextValue}>
      <TableDataContext.Provider value={tableDataContextValue}>
        <Table />
      </TableDataContext.Provider>
    </SelectedCellContext.Provider>
  );
};

const Table = () => {
  const {
    tableData: { tableCol, tableRow, tableContent },
    setTableData,
    tableRef,
  } = useTableData();
  const mouseDownPos = useRef<Point>({ x: 0, y: 0 });

  const handleMouseDown = (event: React.MouseEvent) => {
    event.preventDefault();
    mouseDownPos.current = {
      x: event.clientX,
      y: event.clientY,
    };

    let currentNewCol = 0;
    let currentNewRow = 0;
    const handleMouseMove = (event: MouseEvent) => {
      const offsetX = event.clientX - mouseDownPos.current.x;
      const offsetY = event.clientY - mouseDownPos.current.y;

      let allNewRow = 0,
        allNewCol = 0,
        addOrDeleteRow = 0,
        addOrDeleteCol = 0;
      if (offsetY > 0) {
        allNewRow = Math.floor(offsetY / DefaultCellHeight);
        addOrDeleteRow = allNewRow - currentNewRow;
        currentNewRow = allNewRow;
      }

      if (offsetX > 0) {
        allNewCol = Math.floor(offsetX / DefaultCellWidth);
        addOrDeleteCol = allNewCol - currentNewCol;
        currentNewCol = allNewCol;
      }

      console.log(addOrDeleteRow, addOrDeleteCol);

      if (addOrDeleteRow === 0 && addOrDeleteCol === 0) {
        return;
      }
      setTableData((tableData) => {
        const { tableCol, tableContent, tableRow } = tableData;
        let newTableContent = [...tableContent];
        const newTableRow = [...tableRow];
        const newTableCol = [...tableCol];

        if (addOrDeleteRow !== 0) {
          if (addOrDeleteRow > 0) {
            const newRowId = v4();
            const newRowContent = newTableCol.map((col) => ({
              id: v4(),
              colId: col.id,
              rowId: newRowId,
              content: String(allNewRow),
              colSpan: 1,
              rowSpan: 1,
              isMerged: false,
            }));

            newTableRow.push({ id: newRowId, height: DefaultCellHeight });
            newTableContent.push(newRowContent);
          } else {
            newTableRow.pop();
            newTableContent.pop();
          }
        }

        if (addOrDeleteCol !== 0) {
          const newColId = v4();
          newTableContent = newTableContent.map((rowData, index) => {
            const newRowData = [...rowData];
            if (addOrDeleteCol > 0) {
              newRowData.push({
                id: v4(),
                colId: newColId,
                rowId: newTableRow[index].id,
                content: String(allNewCol),
                colSpan: 1,
                rowSpan: 1,
                isMerged: false,
              });
            } else {
              newRowData.pop();
            }

            return newRowData;
          });

          if (addOrDeleteCol > 0) {
            newTableCol.push({ id: newColId, width: DefaultCellWidth });
          } else {
            newTableCol.pop();
          }
        }

        return {
          tableCol: newTableCol,
          tableRow: newTableRow,
          tableContent: newTableContent,
        };
      });
    };

    const move = throttle(handleMouseMove, 100);
    const handleMouseUp = () => {
      document.removeEventListener('mousemove', move);
      document.removeEventListener('mouseup', handleMouseUp);
    };

    document.addEventListener('mousemove', move);
    document.addEventListener('mouseup', handleMouseUp);
  };

  return (
    <div className='relative'>
      <div className='inline-block relative'>
        <table className='border-t border-l' ref={tableRef}>
          <colgroup>
            {tableCol.map((col) => (
              <col width={col.width} data-col-id={col.id} key={col.id} />
            ))}
          </colgroup>

          <tbody>
            {tableContent.map((row, index) => (
              <Row row={tableRow[index]} key={tableRow[index].id} rowData={row} />
            ))}
          </tbody>
        </table>

        <div
          onMouseDown={handleMouseDown}
          className='absolute -right-4 hover:bg-gray-200 hover:cursor-move'
        >
          <BsArrowDownRight />
        </div>
      </div>

      <div>
        {tableCol.map((col, index) => {
          return (
            <ColResize
              key={col.id}
              col={col}
              left={sum(tableCol.slice(0, index + 1).map((col) => col.width)) - 2}
              tableRef={tableRef}
              setTableData={setTableData}
            />
          );
        })}
        {tableRow.map((row, index) => {
          return (
            <RowResize
              key={index}
              row={row}
              top={sum(tableRow.slice(0, index + 1).map((row) => row.height)) - 2}
              width={sum(tableCol.map((col) => col.width))}
              tableRef={tableRef}
              setTableData={setTableData}
            />
          );
        })}
      </div>
    </div>
  );
};

const Row: FC<{
  rowData: CellData[];
  row: TableRow;
}> = ({ rowData, row }) => {
  return (
    <tr style={{ height: row.height }} data-row-id={row.id}>
      {rowData.map((cell) => (
        <Cell key={cell.id} cell={cell} />
      ))}
    </tr>
  );
};

const Cell: FC<{ cell: CellData }> = ({ cell }) => {
  const { tableRef, setTableData } = useTableData();
  const mouseDownPos = useRef<Point>({ x: 0, y: 0 });
  const [menuPos, setMenuPos] = useState<Point>();
  const tdRef = useRef<HTMLTableCellElement | null>(null);
  const { selectedCell, setSelectedCell } = useSelectedCell();
  const isSelected = selectedCell.find((id) => id === cell.id);

  if (cell.isMerged) return null;

  const findAllCell = (
    selectedCellNode: HTMLTableCellElement[],
    allTableCellNode: NodeListOf<HTMLTableCellElement>,
    allTableCellNodeRect: Map<HTMLTableCellElement, DOMRect>
  ): HTMLTableCellElement[] => {
    if (selectedCellNode.length === 0) return [];
    if (selectedCellNode.length === 1) {
      const cellNode = selectedCellNode[0];
      if (cellNode.colSpan > 1 || cellNode.rowSpan > 1) {
        return selectedCellNode;
      }

      return [];
    }

    let left = Infinity,
      top = Infinity,
      right = -Infinity,
      bottom = -Infinity,
      sumCellArea = 0;
    selectedCellNode.forEach((node) => {
      const rect = allTableCellNodeRect.get(node);
      if (!rect) return;

      sumCellArea += rect.width * rect.height;
      left = Math.min(rect.left, left);
      top = Math.min(rect.top, top);
      right = Math.max(rect.right, right);
      bottom = Math.max(rect.bottom, bottom);
    });

    if (Math.abs((bottom - top) * (right - left) - sumCellArea) > AreaPrecisionOffset) {
      const newSelectArea = {
        left,
        top,
        width: right - left,
        height: bottom - top,
      } as DOMRect;

      const tempNewSelectedCell: HTMLTableCellElement[] = [];
      allTableCellNode.forEach((node) => {
        const rect = allTableCellNodeRect.get(node);
        if (!rect) return;

        const radio = getIntersectionRatio(newSelectArea, rect);
        if (radio > 0) tempNewSelectedCell.push(node);
      });

      return findAllCell(tempNewSelectedCell, allTableCellNode, allTableCellNodeRect);
    }

    return selectedCellNode;
  };

  const handleMouseDown = (event: React.MouseEvent) => {
    event.preventDefault();

    if ((event.button === 0 || (event.button === 2 && !isSelected)) && selectedCell.length > 0) {
      setSelectedCell([]);
    }

    mouseDownPos.current = {
      x: event.clientX,
      y: event.clientY,
    };
    let isMoving = false;
    let tempSelectedCell: HTMLTableCellElement[] = [];

    const handleMouseMove = (event: MouseEvent) => {
      if (!tableRef.current) return;

      if (!isMoving) {
        if (
          Math.abs(event.clientX - mouseDownPos.current.x) < 5 &&
          Math.abs(event.clientY - mouseDownPos.current.y) < 5
        ) {
          return;
        }
        isMoving = true;
      }

      // 碰撞检测 td，给碰撞到的 td 着色
      // 碰撞完成后需要检测当前选中的 cell 是否能组成一个矩形，如果不能，那么需要修正！！！！
      const selectArea = {
        left: Math.min(mouseDownPos.current.x, event.clientX),
        top: Math.min(mouseDownPos.current.y, event.clientY),
        width: Math.abs(mouseDownPos.current.x - event.clientX) || 1,
        height: Math.abs(mouseDownPos.current.y - event.clientY) || 1,
      } as DOMRect;
      let newSelectedCell: HTMLTableCellElement[] = [];
      const allTableCellNode = tableRef.current.querySelectorAll('td');
      const allTableCellNodeRect = new Map<HTMLTableCellElement, DOMRect>();
      // 为避免 mousemove 的过程中不断 setState 导致太多的 render, 所以 move 过程中通过 js 添加高亮，mouseup 之后再 setState
      allTableCellNode.forEach((node) => {
        const rect = node.getBoundingClientRect();
        allTableCellNodeRect.set(node, rect);
        if (node.classList.contains('cell-hover-highlight')) {
          node.classList.remove('cell-hover-highlight');
        }

        if (getIntersectionRatio(selectArea, rect) > 0) {
          newSelectedCell.push(node);
        }
      });

      newSelectedCell = findAllCell(newSelectedCell, allTableCellNode, allTableCellNodeRect);
      newSelectedCell.forEach((node) => node.classList.add('cell-hover-highlight'));
      tempSelectedCell = newSelectedCell;
    };

    const handleMouseUp = () => {
      document.removeEventListener('mousemove', handleMouseMove);
      document.removeEventListener('mouseup', handleMouseUp);

      if (!isMoving) return;

      if (event.button === 0 && tempSelectedCell.length > 0) {
        const newSelectedCell = tempSelectedCell
          .map((node) => node.dataset.cellId ?? '')
          .filter((item): item is string => !!item);
        setSelectedCell(newSelectedCell);
      }
    };

    document.addEventListener('mousemove', handleMouseMove);
    document.addEventListener('mouseup', handleMouseUp);
  };

  const handleContextMenu = (event: React.MouseEvent) => {
    event.preventDefault();
    if (!tdRef.current) return;

    if (isSelected) {
      setMenuPos({
        x: event.clientX,
        y: event.clientY,
      });
    }
  };

  return (
    <>
      <td
        ref={tdRef}
        onMouseDown={handleMouseDown}
        onContextMenu={handleContextMenu}
        className={cx(
          'border-r border-b',
          isSelected && 'cell-hover-highlight',
          (cell.colSpan > 1 || cell.rowSpan > 1) && 'relative z-50'
        )}
        data-cell-id={cell.id}
        colSpan={cell.colSpan > 1 ? cell.colSpan : undefined}
        rowSpan={cell.rowSpan > 1 ? cell.rowSpan : undefined}
      >
        {cell.content}
      </td>
      {menuPos &&
        createPortal(
          <TableRightMenu
            top={menuPos.y}
            left={menuPos.x}
            closeModal={() => setMenuPos(undefined)}
            setTableData={setTableData}
          />,
          document.body
        )}
    </>
  );
};

const ColResize: FC<{
  col: TableCol;
  left: number;
  tableRef: React.MutableRefObject<HTMLTableElement | null>;
  setTableData: React.Dispatch<React.SetStateAction<TableData>>;
}> = ({ col, left, tableRef, setTableData }) => {
  const resizeRef = useRef<HTMLDivElement | null>(null);
  const mouseDownPos = useRef<Point>({ x: 0, y: 0 });
  const handleMouseDown = (event: React.MouseEvent) => {
    event.preventDefault();
    mouseDownPos.current = {
      x: event.clientX,
      y: event.clientY,
    };

    let newWidth = 0;
    const handleMouseMove = (event: MouseEvent) => {
      if (!resizeRef.current) return;
      const colgroup = tableRef.current?.querySelector('colgroup');
      if (!colgroup) return;
      const colNode = colgroup.querySelector(
        `col[data-col-id="${col.id}"]`
      ) as HTMLTableColElement | null;
      if (!colNode) return;

      let offsetX = event.clientX - mouseDownPos.current.x;
      offsetX = clamp(offsetX, -(col.width - MinColWidth), MaxColWidth - col.width);
      newWidth = col.width + offsetX;
      colNode.style.width = `${newWidth}px`;
      resizeRef.current.style.opacity = '1';
      resizeRef.current.style.zIndex = '100';
      resizeRef.current.style.left = `${left + offsetX}px`;
    };
    const handleMouseUp = () => {
      document.removeEventListener('mousemove', handleMouseMove);
      document.removeEventListener('mouseup', handleMouseUp);
      if (resizeRef.current) {
        resizeRef.current.style.opacity = '0';
        resizeRef.current.style.zIndex = 'unset';
      }

      setTableData((tableData) => {
        const newTableCol = tableData.tableCol.map((item) => {
          if (item.id === col.id) {
            return {
              ...item,
              width: newWidth,
            };
          }
          return item;
        });

        return {
          ...tableData,
          tableCol: newTableCol,
        };
      });
    };

    document.addEventListener('mousemove', handleMouseMove);
    document.addEventListener('mouseup', handleMouseUp);
  };

  return (
    <div
      ref={resizeRef}
      onMouseDown={handleMouseDown}
      className='absolute top-0 w-1 bg-black h-full cursor-[col-resize]'
      style={{ left, opacity: 0 }}
    />
  );
};

const RowResize: FC<{
  row: TableRow;
  top: number;
  width: number;
  tableRef: React.MutableRefObject<HTMLTableElement | null>;
  setTableData: React.Dispatch<React.SetStateAction<TableData>>;
}> = ({ row, top, width, tableRef, setTableData }) => {
  const resizeRef = useRef<HTMLDivElement | null>(null);
  const mouseDownPos = useRef<Point>({ x: 0, y: 0 });
  const handleMouseDown = (event: React.MouseEvent) => {
    event.preventDefault();
    mouseDownPos.current = {
      x: event.clientX,
      y: event.clientY,
    };

    let newHeight = 0;
    const handleMouseMove = (event: MouseEvent) => {
      if (!tableRef.current || !resizeRef.current) return;
      const tr = tableRef.current.querySelector(
        `tbody tr[data-row-id="${row.id}"]`
      ) as HTMLElement | null;
      if (!tr) return;

      let offsetY = event.clientY - mouseDownPos.current.y;
      offsetY = clamp(offsetY, -(row.height - MinRowHeight), MaxRowHeight - row.height);
      newHeight = row.height + offsetY;
      tr.style.height = `${newHeight}px`;
      resizeRef.current.style.opacity = '1';
      resizeRef.current.style.zIndex = '100';
      resizeRef.current.style.top = `${top + offsetY}px`;
    };
    const handleMouseUp = () => {
      document.removeEventListener('mousemove', handleMouseMove);
      document.removeEventListener('mouseup', handleMouseUp);
      if (resizeRef.current) {
        resizeRef.current.style.opacity = '0';
        resizeRef.current.style.zIndex = 'unset';
      }

      setTableData((tableData) => {
        const newTableRow = tableData.tableRow.map((item) => {
          if (item.id === row.id) {
            return {
              id: item.id,
              height: newHeight,
            };
          }
          return item;
        });

        return {
          ...tableData,
          tableRow: newTableRow,
        };
      });
    };

    document.addEventListener('mousemove', handleMouseMove);
    document.addEventListener('mouseup', handleMouseUp);
  };

  return (
    <div
      ref={resizeRef}
      onMouseDown={handleMouseDown}
      className='absolute left-0 h-1 bg-black w-full cursor-[row-resize]'
      style={{
        top,
        width,
        opacity: 0,
      }}
    />
  );
};

const TableRightMenu: FC<{
  left: number;
  top: number;
  closeModal: () => void;
  setTableData: React.Dispatch<React.SetStateAction<TableData>>;
}> = ({ left, top, closeModal, setTableData }) => {
  const { selectedCell, setSelectedCell } = useSelectedCell();

  const menuItems = [
    {
      title: '向上插入一行',
      type: 'inset_top',
    },
    {
      title: '向下插入一行',
      type: 'inset_bottom',
    },
    {
      title: '向左插入一行',
      type: 'insert_left',
    },
    {
      title: '向右插入一列',
      type: 'insert_right',
    },
    {
      title: selectedCell.length > 1 ? '合并单元格' : '取消合并单元格',
      type: selectedCell.length > 1 ? 'merge' : 'split',
    },
    {
      title: '删除列',
      type: 'delete_col',
    },
    {
      title: '删除行',
      type: 'delete_row',
    },
  ];

  const handleClick = ({ type }: { type: string }) => {
    setTableData((tableData) => {
      const { tableCol, tableContent, tableRow } = tableData;
      const firstCellId = selectedCell[0];
      let rightColIndex = 0;
      let lastRowIndex = 0;
      let topRowIndex = 0;
      let leftColIndex = 0;
      tableContent.forEach((rowData, rowIndex) => {
        rowData.forEach((cell, colIndex) => {
          if (cell.id === selectedCell[0]) {
            topRowIndex = rowIndex;
            leftColIndex = colIndex;
          }
          if (selectedCell.includes(cell.id)) {
            rightColIndex = Math.max(cell.colSpan + colIndex - 1, rightColIndex);
            lastRowIndex = Math.max(cell.rowSpan + rowIndex - 1, lastRowIndex);
          }
        });
      });

      switch (type) {
        case 'inset_top':
        case 'inset_bottom': {
          const isInsertTop = type === 'inset_top';
          const newRowId = v4();
          const newTableRow = tableRow.slice(0);
          const newTableContent = tableContent.slice();
          const newRowContent = tableCol.map((col) => ({
            id: v4(),
            colId: col.id,
            rowId: newRowId,
            content: '2',
            colSpan: 1,
            rowSpan: 1,
            isMerged: false,
          }));

          newTableRow.splice(isInsertTop ? topRowIndex : lastRowIndex + 1, 0, {
            id: newRowId,
            height: 100,
          });
          newTableContent.splice(isInsertTop ? topRowIndex : lastRowIndex + 1, 0, newRowContent);
          return {
            tableCol,
            tableRow: newTableRow,
            tableContent: newTableContent,
          };
        }
        case 'insert_left':
        case 'insert_right': {
          const isInsertLeft = type === 'insert_left';

          const newColId = v4();
          const newTableCol = tableCol.slice(0);
          const newTableContent = tableContent.map((rowData, index) => {
            const newRowData = [...rowData];
            newRowData.splice(isInsertLeft ? leftColIndex : rightColIndex + 1, 0, {
              id: v4(),
              colId: newColId,
              rowId: tableRow[index].id,
              content: '3',
              colSpan: 1,
              rowSpan: 1,
              isMerged: false,
            });

            return newRowData;
          });

          newTableCol.splice(isInsertLeft ? leftColIndex : rightColIndex + 1, 0, {
            id: newColId,
            width: DefaultCellWidth,
          });
          return {
            tableCol: newTableCol,
            tableRow,
            tableContent: newTableContent,
          };
        }
        case 'merge': {
          const newTableContent = tableContent.map((rowData) => {
            return rowData.map((cell) => {
              if (firstCellId === cell.id) {
                return {
                  ...cell,
                  colSpan: rightColIndex - leftColIndex + 1,
                  rowSpan: lastRowIndex - topRowIndex + 1,
                };
              }

              if (selectedCell.includes(cell.id)) {
                return {
                  ...cell,
                  isMerged: true,
                };
              }

              return cell;
            });
          });
          setSelectedCell([firstCellId]);
          return {
            tableCol,
            tableRow,
            tableContent: newTableContent,
          };
        }
        case 'split': {
          const newSelectedCell: string[] = [];
          const newTableContent = tableContent.map((rowData, rowIndex) => {
            if (rowIndex >= topRowIndex && rowIndex <= lastRowIndex) {
              return rowData.map((cell, colIndex) => {
                if (colIndex >= leftColIndex && colIndex <= rightColIndex) {
                  newSelectedCell.push(cell.id);
                  return {
                    ...cell,
                    colSpan: 1,
                    rowSpan: 1,
                    isMerged: false,
                  };
                }

                return cell;
              });
            }
            return rowData;
          });

          setSelectedCell(newSelectedCell);
          return {
            tableCol,
            tableRow,
            tableContent: newTableContent,
          };
        }
        case 'delete_col': {
          const newTableCol = tableCol.filter(
            (_, index) => index < leftColIndex || index > rightColIndex
          );
          const newTableContent = tableContent.map((rowData) =>
            rowData.filter((_, index) => index < leftColIndex || index > rightColIndex)
          );

          if (newTableCol.length === 0) {
            // TODO：删除表格
          }

          return {
            tableCol: newTableCol,
            tableRow,
            tableContent: newTableContent,
          };
        }
        case 'delete_row': {
          const newTableRow = tableRow.filter(
            (_, index) => index < topRowIndex || index > lastRowIndex
          );
          const newTableContent = tableContent.filter(
            (_, index) => index < topRowIndex || index > lastRowIndex
          );

          return {
            tableCol,
            tableRow: newTableRow,
            tableContent: newTableContent,
          };
        }
        default:
          return tableData;
      }
    });
    closeModal();
  };
  return (
    <div className='fixed w-full h-full top-0 left-0 z-[1000]' onClick={closeModal}>
      <div
        className='absolute py-2 bg-white shadow-lg rounded-md w-[120px]'
        style={{ left: left + 8, top: top + 8 }}
        onClick={(event) => event.stopPropagation()}
      >
        {menuItems.map((item) => {
          return (
            <div
              key={item.type}
              className='text-sm h-10 flex items-center hover:bg-red-100 px-2 cursor-pointer'
              onClick={() => handleClick(item)}
            >
              {item.title}
            </div>
          );
        })}
      </div>
    </div>
  );
};

const initTable = (): TableData => {
  const tableCol = [
    { width: DefaultCellWidth, id: v4() },
    { width: DefaultCellWidth, id: v4() },
    { width: DefaultCellWidth, id: v4() },
  ];
  const tableRow = [
    { height: DefaultCellHeight, id: v4() },
    { height: DefaultCellHeight, id: v4() },
  ];
  const tableContent = [
    [
      {
        id: v4(),
        colId: tableCol[0].id,
        rowId: tableRow[0].id,
        content: '1',
        colSpan: 1,
        rowSpan: 1,
        isMerged: false,
      },
      {
        id: v4(),
        colId: tableCol[1].id,
        rowId: tableRow[0].id,
        content: '1',
        colSpan: 1,
        rowSpan: 1,
        isMerged: false,
      },
      {
        id: v4(),
        colId: tableCol[2].id,
        rowId: tableRow[0].id,
        content: '1',
        colSpan: 1,
        rowSpan: 1,
        isMerged: false,
      },
    ],
    [
      {
        id: v4(),
        colId: tableCol[0].id,
        rowId: tableRow[1].id,
        content: '1',
        colSpan: 1,
        rowSpan: 1,
        isMerged: false,
      },
      {
        id: v4(),
        colId: tableCol[1].id,
        rowId: tableRow[1].id,
        content: '1',
        colSpan: 1,
        rowSpan: 1,
        isMerged: false,
      },
      {
        id: v4(),
        colId: tableCol[2].id,
        rowId: tableRow[1].id,
        content: '1',
        colSpan: 1,
        rowSpan: 1,
        isMerged: false,
      },
    ],
  ];

  return {
    tableCol,
    tableRow,
    tableContent,
  };
};

export const findIndexInParentElement = (element: HTMLElement) => {
  let index = 0;
  let tempElement: HTMLElement | null = element.previousElementSibling as HTMLElement | null;
  while (tempElement != null) {
    index++;
    tempElement = tempElement.previousElementSibling as HTMLElement | null;
  }

  return index;
};

/**
 * 参考石墨，ones，语雀，都是用的 table，只有用 table 才能很好的解决跨行和跨列的问题。
 * 否则只有通过绝对定位对每一个cell进行定位, 才能实现跨行的效果
 *
 * 行和列都需要id，否则在上下插入行的时候就没有办法准确定位之前被选中的元素，或者就是在插入的时候同时修改 selectedCell 的数据，麻烦了，不易理解和维护
 * z-index：只在脱离了文档流的元素(定位元素)上起作用，所以 z-index 总是配合 position 和 float 一起使用。https://www.cnblogs.com/wuxunyan/p/9282447.html
 *
 * 拖拽过程中的 ui 变化使用 dom api 去操作，拖拽结束后的状态保持使用 state 去控制，可以避免 dom 的频繁 render。
 * 难点：在框选表格的过程中，使用面积比较法去选择所有应该被选中的单元格
 */
