import { throttle } from 'lodash-es';
import React, { useEffect, useState, useRef, useCallback } from 'react';
import type { FC, ReactNode } from 'react';
import { TextLayerBuilder } from './text-layer';
import isHotkey from 'is-hotkey';
import { AiOutlineDown, AiOutlineUp } from 'react-icons/ai';
import { Page } from './page';
import { loadJs } from '../../utils/load-resource';
import { cx } from '../../utils/classnames';
import './style.css';

export const DEFAULT_PAGE_WIDTH = 800;
export const DEFAULT_PAGE_HEIGHT = 1130;
export const CONTAINER_MIN_PADDING = 20;
export const CONTAINER_MARGIN_TOP = 55;

class PDFLoader {
  private disposed = false;
  private pdf: any;

  constructor(private file: string) {}

  async load() {
    await loadJs(`/pdf/pdf.js`);

    return (window as any).pdfjsLib
      .getDocument({
        url: this.file,
        // https://stackoverflow.com/questions/32764773/what-is-a-pdf-bcmap-file/40634723
        cMapUrl: `/pdf/cmaps/`,
        cMapPacked: true,
      })
      .promise.then((pdf: any) => {
        this.pdf = pdf;
        if (this.disposed) {
          pdf.destroy();
        }
        return pdf;
      });
  }

  dispose() {
    this.disposed = true;
    if (this.pdf) {
      this.pdf.destroy();
    }
  }
}

const file =
  'https://hexiang-images.oss-cn-beijing.aliyuncs.com/rxjs%20%E4%B8%AD%E6%96%87%E6%96%87%E6%A1%A3.pdf';

export const PdfPreview: FC<{ fileUrl: string }> = ({ fileUrl = file }) => {
  const pdfDocumentRef = useRef<any>();
  const isOnPageNumber = useRef(false);
  const pageNumberRef = useRef<HTMLDivElement>(null);
  const pdfContainerRef = useRef<HTMLDivElement>(null);
  const scrollContainerRef = useRef<HTMLDivElement>(null);
  const inputRef = useRef<HTMLInputElement>(null);
  const pageNumTimer = useRef<NodeJS.Timeout>();
  const [loading, setLoading] = useState(true);
  const [isShowPageNum, setIsShowPageNum] = useState(false);
  const [currentPage, setCurrentPage] = useState<string>('1');

  useEffect(() => {
    const loader = new PDFLoader(fileUrl);

    void loader.load().then((pdf) => {
      pdfDocumentRef.current = pdf;
      setLoading(false);
    });

    return () => {
      loader.dispose();
      clearPageNumTimer();
    };
  }, [fileUrl]);

  const drawPage = useCallback((pageNum: number) => {
    if (!pdfContainerRef.current) return;

    const pageContainer = pdfContainerRef.current.querySelector(
      `div[id=page-${pageNum}]`
    ) as HTMLDivElement | null;

    if (!pageContainer) return;
    pageContainer.innerHTML = '';

    pdfDocumentRef.current.getPage(pageNum).then((page: any) => {
      if (!scrollContainerRef.current) return;
      const viewport = page.getViewport({ scale: (window.devicePixelRatio || 1) * 1.5 });
      const canvas = document.createElement('canvas');
      const context = canvas.getContext('2d');
      if (!context) return;

      const scrollContainerWidth = scrollContainerRef.current.clientWidth;
      const radio =
        viewport.width /
        Math.min(DEFAULT_PAGE_WIDTH, scrollContainerWidth - CONTAINER_MIN_PADDING * 2);
      pageContainer.appendChild(canvas);
      canvas.height = viewport.height;
      canvas.width = viewport.width;

      const cssWidth = canvas.width / radio;
      const cssHeight = canvas.height / radio;
      pageContainer.style.width = `${cssWidth}px`;
      pageContainer.style.height = `${cssHeight}px`;
      canvas.style.width = `${cssWidth}px`;
      canvas.style.height = `${cssHeight}px`;

      page
        .render({
          canvasContext: context,
          viewport,
        })
        .promise.then(() => page.getTextContent())
        .then((textContent: any) => {
          const textLayerDiv = document.createElement('div');
          textLayerDiv.setAttribute('class', 'textLayer');
          textLayerDiv.style.transform = `scale(${1 / radio})`;
          textLayerDiv.style.width = `${canvas.width}px`;
          textLayerDiv.style.height = `${canvas.height}px`;
          pageContainer.appendChild(textLayerDiv);

          // eslint-disable-next-line @typescript-eslint/ban-ts-comment
          // @ts-ignore
          const textLayer = new TextLayerBuilder({
            textLayerDiv,
            pageIndex: page.pageIndex,
            viewport,
          });

          textLayer.setTextContent(textContent);
          textLayer.render();
        });
    });
  }, []);

  const jumpPage = (page: string) => {
    if (!scrollContainerRef.current) return;
    const targetPage = document.querySelector(`#page-${page}`);
    if (!targetPage) return;

    scrollContainerRef.current.scrollTo(0, (targetPage as HTMLDivElement).offsetTop);

    setCurrentPage(page);
  };

  const onEnter = () => {
    const pageNum = parseInt(currentPage, 10);
    if (Number.isNaN(pageNum) || pageNum > pdfDocumentRef.current.numPages) {
      alert('输入页码无效！');
    } else {
      if (currentPage !== pageNum.toString()) {
        setCurrentPage(pageNum.toString());
      }
      jumpPage(pageNum.toString());
    }
  };

  const clearPageNumTimer = () => {
    if (pageNumTimer.current) {
      clearTimeout(pageNumTimer.current);
    }
  };

  const controlPageNumVisible = (event?: React.MouseEvent) => {
    if (event) {
      if (pageNumberRef.current?.contains(event.target as HTMLDivElement)) {
        isOnPageNumber.current = true;
        setIsShowPageNum(true);
        clearPageNumTimer();
        return;
      }
      isOnPageNumber.current = false;
    }

    if (isOnPageNumber.current) {
      return;
    }

    const setHidePageNumTimer = () => {
      pageNumTimer.current = setTimeout(() => {
        clearPageNumTimer();
        if (inputRef.current === document.activeElement) {
          setHidePageNumTimer();
        } else {
          setIsShowPageNum(false);
        }
      }, 1000);
    };

    if (!isShowPageNum) {
      setIsShowPageNum(true);
      setHidePageNumTimer();
    } else {
      clearPageNumTimer();
      setHidePageNumTimer();
    }
  };

  const handleScroll = () => {
    if (inputRef.current === document.activeElement) {
      inputRef.current?.blur();
    }

    controlPageNumVisible();

    const scrollContainer = scrollContainerRef.current;
    if (!scrollContainer) return;

    const element = document.elementFromPoint(
      scrollContainer.clientWidth / 2,
      scrollContainer.clientHeight / 2 + CONTAINER_MARGIN_TOP
    );
    if (!element) return;
    const page = element.closest('div[id^=page]');
    if (!page) return;
    const pageNum = page.id.split('-')[1];
    if (pageNum) {
      setCurrentPage(pageNum);
    }
  };

  const renderPages = () => {
    if (loading || !pdfDocumentRef.current || !scrollContainerRef.current) return null;
    const pages: ReactNode[] = [];

    for (let pageNum = 1; pageNum < pdfDocumentRef.current.numPages + 1; pageNum++) {
      pages.push(
        <Page
          drawPage={drawPage}
          pageNum={pageNum}
          key={pageNum}
          container={scrollContainerRef.current}
        />
      );
    }
    return pages;
  };

  return (
    <div className='w-full h-[750px] relative'>
      <div
        ref={scrollContainerRef}
        className='w-full h-full overflow-y-auto flex justify-center pt-2 bg-black/70 border'
        onScroll={throttle(handleScroll, 300)}
        onMouseMove={throttle(controlPageNumVisible, 300)}
      >
        <div ref={pdfContainerRef} style={{ height: 'max-content' }}>
          {renderPages()}
        </div>
        <div
          ref={pageNumberRef}
          className={cx(
            'absolute z-50 flex items-center h-12 px-2 text-white transition-all duration-200 -translate-x-1/2 rounded-sm bottom-4 left-1/2 bg-black '
          )}
        >
          <button className='flex items-center justify-center mr-1 rounded animate-hover hover:bg-white hover:bg-opacity-10'>
            <AiOutlineUp
              className='p-1 w-full'
              onClick={() => jumpPage(String(Number(currentPage) - 1))}
            />
          </button>
          <div className='relative px-0.5'>
            <span className='invisible'>{String(currentPage)}</span>
            <input
              ref={inputRef}
              type='text'
              className='absolute left-0 top-0 h-full w-full px-0 bg-transparent border-none focus:border-0 '
              style={{ backgroundColor: 'transparent' }}
              value={currentPage}
              onKeyDown={(event) => {
                if (isHotkey('enter')(event)) {
                  onEnter();
                }
              }}
              onChange={(e) => {
                setCurrentPage(e.target.value);
              }}
            />
          </div>
          &nbsp;/&nbsp;&nbsp;<span> {pdfDocumentRef.current?.numPages} </span>
          <button className='flex items-center justify-center ml-1 rounded animate-hover hover:bg-white hover:bg-opacity-10'>
            <AiOutlineDown
              size='large'
              className='p-1 w-full'
              onClick={() => jumpPage(String(Number(currentPage) + 1))}
            />
          </button>
        </div>
        {loading && <>loading...</>}
      </div>
    </div>
  );
};
