import { throttle } from 'lodash-es';
import React, { useRef } from 'react';
import { autoScrollY, rafY } from '../utils/auto-scroll';
import { getIntersectionRatio } from '../utils/intersection-ratio';

interface PosCache {
  left: number;
  width: number;
  height: number;
  offsetTop: number;
  node: HTMLElement;
}

export const Selector = () => {
  const container = useRef<HTMLDivElement | null>(null);
  const nodePosCache = useRef(new Map<string, PosCache>());
  const mouseDownPos = useRef<{ x: number; y: number; scrollTop: number }>({
    x: 0,
    y: 0,
    scrollTop: 0,
  });

  const handleMouseDown = (event: React.MouseEvent) => {
    event.preventDefault();
    const containerNode = container.current;
    if (!containerNode) return;

    nodePosCache.current = new Map();

    mouseDownPos.current = {
      x: event.clientX,
      y: event.clientY,
      scrollTop: containerNode.scrollTop,
    };

    let lastCursorPoint = { x: event.clientX, y: event.clientY };
    let isMoving = false;
    const selectArea = document.createElement('div');
    selectArea.style.position = 'fixed';
    selectArea.style.background = 'rgba(0, 250, 0, 0.3)';

    const highlight = () => {
      const firstElementChildRect = containerNode.firstElementChild?.getBoundingClientRect();
      if (!firstElementChildRect) return;

      const allSubNodes = containerNode.querySelectorAll('.sub-node') as NodeListOf<HTMLElement>;

      // TODO:含有虚拟列表的框选优化，需要存储位置
      for (const node of allSubNodes) {
        const uuid = node.dataset.uuid;
        if (!uuid) continue;

        if (nodePosCache.current.has(uuid)) continue;

        const rect = node.getBoundingClientRect();
        const pos = {
          left: rect.left,
          width: rect.width,
          height: rect.height,
          offsetTop: rect.top - firstElementChildRect.top,
          node,
        };

        nodePosCache.current.set(uuid, pos);
      }

      const selectorRect = {
        left: parseInt(selectArea.style.left),
        top: parseInt(selectArea.style.top),
        width: parseInt(selectArea.style.width),
        height: parseInt(selectArea.style.height),
      } as DOMRect;

      const allSelectedIds = new Set();
      for (const [uuid, pos] of nodePosCache.current) {
        const nodeRect = {
          ...pos,
          top: pos.offsetTop + firstElementChildRect.top,
        };

        // 没有虚拟列表的时候可以通过  pos.node 获取节点，因为节点一直在
        // pos.node.classList.add('selected-node');

        // 有了虚拟列表通过  pos.node 取节点就不可行了，需要每次去查询
        // const node = document.querySelector(`[data-uuid="${uuid}"]`) as HTMLElement | null;
        // ！！！！querySelector 性能很差，不要用这个！！！1. 单行监听被选中的 id 2. 再遍历一次 allSubNodes
        // if (!node) return;
        if (getIntersectionRatio(nodeRect, selectorRect) > 0) {
          allSelectedIds.add(uuid);
          // node.classList.add('selected-node');
        } else {
          // pos.node.classList.remove('selected-node');
        }
      }

      for (const node of allSubNodes) {
        const uuid = node.dataset.uuid;
        if (!uuid) continue;

        if (allSelectedIds.has(uuid)) {
          node.classList.add('selected-node');
        } else {
          if (node.classList.contains('selected-node')) {
            node.classList.remove('selected-node');
          }
        }
      }
    };

    const handleMouseMove = (event: MouseEvent) => {
      if (!isMoving) {
        isMoving = true;
        document.body.appendChild(selectArea);
      }

      selectArea.style.width = `${Math.max(Math.abs(event.clientX - mouseDownPos.current.x), 1)}px`;
      selectArea.style.left = `${Math.min(event.clientX, mouseDownPos.current.x)}px`;
      const scrollDis = containerNode.scrollTop - mouseDownPos.current.scrollTop;
      const realMouseDownY = mouseDownPos.current.y - scrollDis;
      selectArea.style.top = `${Math.min(event.clientY, realMouseDownY)}px`;
      selectArea.style.height = `${Math.abs(event.clientY - realMouseDownY)}px`;

      autoScrollY({ y: event.clientY, container: containerNode });
      lastCursorPoint = { x: event.clientX, y: event.clientY };

      highlight();
    };

    const moveEvent = throttle(handleMouseMove, 60);

    const handleMouseUp = () => {
      containerNode.removeEventListener('scroll', handleScroll);
      document.removeEventListener('mousemove', moveEvent);
      document.removeEventListener('mouseup', handleMouseUp);
      cancelAnimationFrame(rafY);
      selectArea.remove();
    };

    const handleScroll = () => {
      if (!isMoving) return;

      const scrollDis = containerNode.scrollTop - mouseDownPos.current.scrollTop;
      const realMouseDownY = mouseDownPos.current.y - scrollDis;
      selectArea.style.top = `${Math.min(lastCursorPoint.y, realMouseDownY)}px`;
      selectArea.style.height = `${Math.abs(lastCursorPoint.y - realMouseDownY)}px`;
      highlight();
    };

    containerNode.addEventListener('scroll', handleScroll);
    document.addEventListener('mousemove', moveEvent);
    document.addEventListener('mouseup', handleMouseUp);
  };

  const scrollHeight = 100000;
  const lineHeight = 40;

  return (
    <div className='w-full h-full overflow-auto' ref={container}>
      <div
        onMouseDown={handleMouseDown}
        className='border border-gray-200'
        style={{ height: scrollHeight }}
      >
        {new Array(Math.floor(scrollHeight / lineHeight)).fill(1).map((_, index) => {
          return (
            <div
              key={index}
              className='border-b border-gray-200 sub-node'
              style={{ height: lineHeight }}
              data-uuid={index + 1}
            >
              {index + 1}
            </div>
          );
        })}
      </div>
    </div>
  );
};

/**
 * scroll 的时候怎么触发 mousemove？？ 当滚动容器滚动的时候，如果鼠标不同不会触发 mousemove，但是业务逻辑都在 mousemove。监听scroll
 *
 * 监听 scroll 的时候怎么更新 node？
 * 记住每一次 mousemove 触发时的坐标，在 scroll 的时候再根据 scrollTop 去更新。
 */
