/**
 * 原地算法：in-place algorithm 不需要借助额外的数据结构就能对输入的数据进行变换的算法
 * wiki: https://en.wikipedia.org/wiki/In-place_algorithm
 * 典型例子：将一个数组中的所有元素反转
 *
 * 冒泡，选择，插入排序都是原地排序。
 */

export function revert(arr: number[]) {
  // 时间复杂度 O(n) 空间复杂度 O(0)
  for (let i = 0; i < Math.floor(arr.length / 2); i++) {
    const temp = arr[i];
    arr[i] = arr[arr.length - 1 - i];
    arr[arr.length - i - 1] = temp;
  }

  return arr;
}

export function arraySwap<T>(arr: T[], from: number, to: number) {
  const temp = arr[from];
  arr[from] = arr[to];
  arr[to] = temp;
  return arr;
}

export function arrayMove<T>(array: T[], from: number, to: number) {
  const newArray = array.slice();
  // 第一个参数已经计算好了位置，第三个参数计算要插入的数据，此时虽然改变了数组，但是不影响第一个参数。
  newArray.splice(to < 0 ? newArray.length + to : to, 0, newArray.splice(from, 1)[0]);
  return newArray;
}

/**
 * 对有序数组排序，时间复杂度 O(n), 空间复杂度O(1)
 * 重点是知道 简单的用 index 变量就能表示一个新数组了
 * @param array
 */

export function removeRepeat<T>(array: T[]): number {
  // 边界判断
  if (array.length === 0) return 0;

  let index = 0;
  for (let i = 0; i < array.length; i++) {
    if (array[index] !== array[i]) {
      index++;
      array[index] = array[i];
    }
  }

  // const newArray = array.slice(0, index + 1)
  return index + 1;
}

window.fn = {
  ...(window.fn ?? {}),
  arrayMove,
  arraySwap,
  revert,
  removeRepeat,
};
