import type { FC } from 'react';
import { memo, useEffect, useRef } from 'react';
import { DEFAULT_PAGE_HEIGHT, DEFAULT_PAGE_WIDTH } from '.';
import './style.css';

interface PageProps {
  pageNum: number;
  container: HTMLDivElement;
  drawPage: (pageNum: number) => void;
}

export const Page: FC<PageProps> = memo(({ container, pageNum, drawPage }) => {
  const pageRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    if (!pageRef.current) return;

    const observer = new IntersectionObserver(
      (entries, observer) => {
        entries.forEach((entry) => {
          if (entry.isIntersecting) {
            drawPage(Number(pageNum));
            observer.disconnect();
          }
        });
      },
      {
        root: container,
        rootMargin: '800px 0px',
      }
    );

    observer.observe(pageRef.current);

    return () => observer.disconnect();
  }, [container, drawPage, pageNum]);

  return (
    <div
      ref={pageRef}
      id={`page-${pageNum}`}
      className='relative bg-white mb-4 mx-auto'
      style={{ width: DEFAULT_PAGE_WIDTH, height: DEFAULT_PAGE_HEIGHT }}
    >
      loading
    </div>
  );
});
