import React, { useEffect } from 'react';
import ReactDOM from 'react-dom';
import { BrowserRouter, Route, Switch } from 'react-router-dom';
import './algorithm';
import { storage } from './algorithm/storage';
import { usePerformance } from './hooks/use-performance';
import { Main } from './main';
import './index.css';

declare global {
  interface Window {
    // eslint-disable-next-line @typescript-eslint/no-explicit-any
    [key: string]: any;
  }
}

export const App = () => {
  usePerformance();

  useEffect(() => () => storage.destroy());
  return (
    <React.StrictMode>
      <BrowserRouter>
        <Switch>
          <Route path='/' component={Main} />
        </Switch>
      </BrowserRouter>
    </React.StrictMode>
  );
};

ReactDOM.render(<App />, document.getElementById('root'));

// git 三大区: 工作区，暂存区，版本库
// 之前这个文件是 App.tsx, 手动改为 app.tsx 后，git 没有检测到变化？
// 解决方案：
//   1. git config core.ignorecase false  让 git 可以区分大小写，但是这只会让该文件作为新文件被提交，之前的文件仍然存在。
//   2. git rm -f filename 将文件从 git 中删除

// 如果在 git 目录中新建文件，那么文件处于 untracked 状态。使用 git rm --cache filename 可以将处于 git版本库中的文件恢复至此状态
// git rm filename: 直接删除文件，包括工作区。如果当前文件有修改，则使用 -f 可以强制删除，--cache 从git版本库中删除但是保留在工作区。
