const MONTHS = '一月_二月_三月_四月_五月_六月_七月_八月_九月_十月_十一月_十二月'.split('_');
export const OneDayMillisecond = 24 * 60 * 60 * 1000;
/**
 * 该方法根据给定的基准时间计算一个时间范围
 * @param base 计算时间范围的基准时间
 * @param length 时间范围的大小
 * @param isBackward 是否基于基准时间向后取时间
 * @param includeBase 是否包含基准时间
 * @returns
 */
export enum DateDir {
  PREV = 'prev',
  NEXT = 'next',
}

export enum ShowType {
  MONTH = 'month',
  YEAR = 'year',
}

interface DatesParams {
  base?: number;
  dir?: DateDir;
  length?: number;
  includeBase?: boolean;
  showType?: ShowType;
}
export const getDateRange = ({
  base = Date.now(),
  dir = DateDir.NEXT,
  length = 100,
  includeBase = false,
  showType = ShowType.MONTH,
}: DatesParams): number[] => {
  const dates: number[] = [];
  const isNext = dir === DateDir.NEXT;

  let baseDate = getDateTimeStamp(base);
  let dateStep = OneDayMillisecond;
  if (showType === ShowType.YEAR) {
    const weekDay = new Date(baseDate).getDay() || 7;
    baseDate = baseDate + (7 - weekDay) * OneDayMillisecond;
    dateStep = OneDayMillisecond * 7;
  }

  const newLength = includeBase ? length : length + 1;
  for (let i = 0; i < newLength; i++) {
    if (!includeBase && i === 0) continue;

    if (isNext) {
      const time = baseDate + i * dateStep;
      dates.push(time);
    } else {
      const time = baseDate - i * dateStep;
      dates.unshift(time);
    }
  }

  return dates;
};

export const getTimelineDateRange = ({ showType, length, base }: DatesParams) => {
  return [
    ...getDateRange({ base, dir: DateDir.PREV, showType, length }),
    ...getDateRange({ base, dir: DateDir.NEXT, showType, length, includeBase: true }),
  ];
};

export const getDateTimeStamp = (time: number) => {
  return new Date(new Date(time).toDateString()).getTime();
};

export const formatDate = (time: number) => {
  const date = new Date(time);
  return {
    year: date.getFullYear(),
    month: date.getMonth() + 1,
    date: date.getDate(),
    day: date.getDay() + 1,
    monthDesc: MONTHS[date.getMonth()],
    time,
  };
};

export const isToday = (time: number) => time === getDateTimeStamp(Date.now());
