import { FaBeer } from 'react-icons/fa';
import * as AllIcons from '../../assets/icon';

export const Icons = () => {
  return (
    <div className='text-gray-900 text-xl flex'>
      <FaBeer className='mr-2' />
      {Object.keys(AllIcons).map((dd: string) => {
        // @ts-ignore
        const Com = AllIcons[dd];
        return <Com key={dd} className='mr-2' />;
      })}
    </div>
  );
};

