import { useEffect } from 'react';

let permission: NotificationPermission;

/**
 * 1. Notification 仅在 https 下生效
 * 2. 有些浏览器需要用户手动触发通知(firefox)，比如放在 onClick 事件里面，所以在发送通知前最好统一检测
 * 3. 发送通知后浏览器无显示，此时查看系统设置，是否开启 浏览器 的通知[banner 和 alert 的区别就是 banner 是否自动消失]
 * @param title
 * @param desc
 */
const sendNotification = async (title: string, desc: string) => {
  await checkPermission();
  if (permission === 'granted') {
    new Notification(title, { dir: 'ltr', body: desc });
  }
};

const checkPermission = async () => {
  switch (Notification.permission) {
    case 'default': {
      permission = await Notification.requestPermission();
      break;
    }
    case 'granted':
      permission = 'granted';
      break;
    case 'denied':
      permission = 'denied';
      break;
  }
};

export const NotificationDemo = () => {
  useEffect(() => {
    checkPermission();
  }, []);

  const handleClick = () => {
    sendNotification('通知标题', '通知描述');
  };
  return (
    <div>
      <button onClick={handleClick}>点击发送通知</button>
    </div>
  );
};
