// eslint-disable-next-line @typescript-eslint/no-explicit-any
export type NormalType = string | number | boolean | null | undefined | Record<string, any>;

interface SaveData {
  data: NormalType;
  expires: number;
}

/**
 * 1. 保证使用 localStorage 存储数据可以存进去，不会因为之前存储的数据太多导致无法存入。【前提：当前存储的数据不超过浏览器存储上限，超出则会报错】
 * 2. 保证超过过期时间的数据会被清除
 * 3. 保证每次清除的是不常用的 key
 */

const DEFAULT_LIVE_TIME = 7 * 24 * 60 * 60 * 1000; // 存储数据默认存活时间 7 天
const CHECK_INTERVAL_TIME = 10 * 60 * 1000; // 10 分钟检测一次

export class CustomStorage {
  private cache: Set<string>;
  private remainKeys: Set<string>;
  private clearLength = 10;
  private timer: NodeJS.Timeout | undefined;
  private liveTime = DEFAULT_LIVE_TIME;
  private checkIntervalTime = CHECK_INTERVAL_TIME;

  constructor(remainKeys: string[] = ['auth']) {
    this.remainKeys = new Set(remainKeys);
    this.cache = new Set(Object.keys(localStorage));
    this.checkCacheValidity();
  }

  setItem(key: string, value: NormalType | SaveData) {
    try {
      if (typeof value === 'object' && value?.expires) {
        value = JSON.stringify(value);
      } else {
        value = JSON.stringify({
          data: value,
          expires: Date.now() + this.liveTime,
        });
      }
    } catch (error) {
      throw new Error('参数错误！');
    }

    this.save(key, value);
  }

  getItem(key: string) {
    let value: NormalType;
    value = localStorage.getItem(key);
    this.updateKeyPos(key);

    if (!value) return value;

    try {
      value = JSON.parse(value);
    } catch (error) {
      if (value === 'undefined') {
        value = undefined;
      }
    }

    if (typeof value === 'object' && value !== null) {
      return value.data;
    }

    return value;
  }

  save = (key: string, value: string) => {
    try {
      localStorage.setItem(key, value);
      this.cache.add(key); // 走到这里的时候已经存储成功了
    } catch (error) {
      if (!this.isExceedSize()) {
        this.removeSomeKeys();
        this.save(key, value);
      } else {
        console.groupCollapsed('%c数据太大！超出存储上限！', 'color: red;');
        console.log(JSON.parse(value));
        console.groupEnd();
      }
    }
  };

  isExceedSize() {
    if (this.cache.size > this.remainKeys.size) {
      return false;
    } else {
      for (const key of this.cache) {
        if (!this.remainKeys.has(key)) {
          return false;
        }
      }

      return true;
    }
  }

  removeSomeKeys() {
    console.groupCollapsed('%c空间不够删除不常用数据', 'color: green;');
    let count = 0;
    for (const key of this.cache) {
      if (!this.remainKeys.has(key)) {
        console.log(key);
        this.remove(key);
        count++;
        if (count >= this.clearLength) break;
      }
    }
    console.groupEnd();
  }

  updateKeyPos(key: string) {
    this.cache.delete(key);
    this.cache.add(key);
  }

  checkCacheValidity = () => {
    console.groupCollapsed('%c 清除过期数据', 'color: blue;');
    for (const key of this.cache) {
      if (this.remainKeys.has(key)) continue;

      const value = localStorage.getItem(key);
      if (!value) continue;

      try {
        const data = JSON.parse(value);
        if (Object.prototype.toString.call(data) !== '[object Object]') continue;

        if (Date.now() > data.expires) {
          console.log(key);
          this.remove(key);
        }
        // eslint-disable-next-line no-empty
      } catch (error) {}
    }
    console.groupEnd();

    this.timer = setTimeout(this.checkCacheValidity, this.checkIntervalTime);
  };

  setLiveTime(time: number) {
    this.liveTime = time;
  }

  setCheckIntervalTime(time: number) {
    this.checkIntervalTime = time;
    if (this.timer) {
      clearTimeout(this.timer);
    }
    this.timer = setTimeout(this.checkCacheValidity, this.checkIntervalTime);
  }

  resetTime() {
    this.liveTime = DEFAULT_LIVE_TIME;
    this.checkIntervalTime = CHECK_INTERVAL_TIME;
    if (this.timer) {
      clearTimeout(this.timer);
    }
    this.timer = setTimeout(this.checkCacheValidity, this.checkIntervalTime);
  }

  destroy() {
    this.clear();
    if (this.timer) {
      clearTimeout(this.timer);
    }
  }

  remove(key: string) {
    this.cache.delete(key);
    localStorage.removeItem(key);
  }

  clear() {
    this.cache.clear();
    localStorage.clear();
  }
}

export const storage = new CustomStorage(['bigData', 'expires', 'exceedSize', 'dataType']);
