import { useEffect, useRef } from 'react';

export const Canvas = () => {
  const canvasRef = useRef<HTMLCanvasElement>(null);

  useEffect(() => {
    if (!canvasRef.current) return;

    // init canvas
    const canvas = canvasRef.current;
    const radio = getDeviceRadio();
    const canvasStyle = getComputedStyle(canvas);
    canvas.width = Math.floor(parseInt(canvasStyle.getPropertyValue('width')) * radio);
    canvas.height = Math.floor(parseInt(canvasStyle.getPropertyValue('height')) * radio);

    const ctx = canvasRef.current.getContext('2d');
    if (!ctx) return;
    ctx.scale(radio, radio);
    // 在 canvas 中一个单位实际上就是一个 canvas 像素。这里是在改变单位对应的 canvas 像素，比如 radio 是 2，那么一个单位就会对应两个 canvas 像素
    ctx.font =
      '20px -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif, PingFang SC';

    // 1. 绘制房子
    // Set line width
    ctx.lineWidth = 10;

    // Wall 绘制矩形，只需要知道一个点坐标和矩形的宽高。描边绘制
    ctx.strokeRect(75, 140, 150, 110);

    // Door 填充绘制
    ctx.fillRect(130, 190, 40, 60);

    // Roof 开始走路径，跳到某个点，然后开始绘制
    ctx.beginPath();
    ctx.moveTo(50, 140);
    ctx.lineTo(150, 60);
    ctx.lineTo(250, 140);
    ctx.closePath();
    ctx.stroke();

    // 2. 清除某部分区域
    const newStartPoint = translate({ x: 75, y: 140 }, 250, 0);
    ctx.clearRect(newStartPoint.x, newStartPoint.y, 150, 110);

    // 3. 写入文字
    ctx.fillText('this is a house', 80, 300);

    // const gridSize = 50;
    // const canvasWidth = ctx.canvas.width;
    // const canvasHeight = ctx.canvas.height;
    // const row = Math.floor(canvasWidth / gridSize);
    // const col = Math.floor(canvasWidth / gridSize);
    // for (let i = 0; i < row; i++) {
    //   ctx.beginPath();
    //   ctx.moveTo(0, i * gridSize - 0.5);
    //   ctx.lineTo(canvasWidth, i * gridSize - 0.5);
    //   ctx.strokeStyle = "#ccc";
    //   ctx.stroke();
    // }
    // for (let i = 0; i < col; i++) {
    //   ctx.beginPath();
    //   ctx.moveTo(i * gridSize - 0.5, 0);
    //   ctx.lineTo(i * gridSize - 0.5, canvasHeight);
    //   ctx.strokeStyle = "#ccc";
    //   ctx.stroke();
    // }
  }, []);

  return (
    <div className='border'>
      <canvas ref={canvasRef} style={{ width: 600, height: 600 }}></canvas>
    </div>
  );
};

/**
 * canvas 可用于 绘制形状，文字，图像。使用了 canvas 的产品：石墨文档，腾讯文档，维格表
 *
 * canvas 元素的宽高：https://developer.mozilla.org/en-US/docs/Web/API/HTMLCanvasElement
 * 默认是为画布设置的，如果没有显示设置 CSS 宽高，那么将会画布宽高也会作为 CSS 宽高。
 * If no [separate] CSS width is assigned to the <canvas>, then this value will also be used as the width of the
 *  canvas in the length-unit CSS Pixel.
 *
 * CanvasRenderingContext2D：CanvasRenderingContext2D
 *
 * 画笔样式：strokeStyle lineWidth lineJoin lineCap(线端点)
 * 字体样式：font(和CSS相同，大小 颜色 字体族)
 * 添加阴影：shadowXXX
 * 填充样式：fillStyle
 */

interface Point {
  x: number;
  y: number;
}

const translate = (point: Point, disX = 0, disY = 0) => {
  return {
    x: point.x + disX,
    y: point.y + disY,
  };
};

const getDeviceRadio = () => {
  return window.devicePixelRatio;
};
