export const readTextFromBlob = (blob: Blob) => {
  return new Promise<string>((resolve, reject) => {
    const reader = new FileReader();
    reader.readAsDataURL(blob);
    reader.onload = () => {
      const base64Str = reader.result;
      if (typeof base64Str !== 'string') {
        reject();
        return;
      }

      const str = atob(base64Str.split(';base64,')[1] ?? '');
      const { encoding } = window.jschardet.detect(str);

      const textReader = new FileReader();
      textReader.readAsText(blob, encoding);
      textReader.onload = () => {
        if (typeof textReader.result === 'string') {
          resolve(textReader.result);
          return;
        }

        reject();
      };
    };
  });
};
