// dnd 的概念：一切都是数据驱动
// 当一个组件或者dom节点在拖拽的时候，拖拽的不是 dom，而是 确定类型的 item
// item：描述了一个正在被拖拽的对象
//将拖动的数据描述为普通对象有助于保持组件之间的解耦和互不了解。
// 通过 type 指明哪些类型是相互之间兼容的。这个在应用变得越来越大的时候很有用
// monitors 用来存储内部状态，比如是否在拖拽过程中

// a drag source, a drag preview, or a drop target

// Collector 对于需要跟踪拖放状态的每个组件，您可以定义一个收集函数 collect，从监视器获得对应的数据，dnd 定时调用返回数据给组件作为属性

// connector：用来连接哪些元素是可以拖拽的胡后缀放置的

// 鼠标事件
// 触摸事件
// import { useSpring, animated } from "react-spring";
import { useDrag } from '@use-gesture/react';
import { clamp } from 'lodash-es';
import { useRef, useState } from 'react';

export const Gesture = () => {
  const containerRef = useRef<HTMLDivElement>(null);
  const lineRef = useRef<HTMLDivElement>(null);
  // const props = useSpring({ from: { opacity: 0 }, to: { opacity: 1 } });
  // return <animated.div style={props}>I will fade in</animated.div>;

  // 1. simple drag
  const [width, setWidth] = useState(200);
  const MinWidth = 100,
    MaxWidth = 600;
  const bind = useDrag(
    ({ offset: [x] }) => {
      if (!containerRef.current) return;
      containerRef.current.style.width = `${clamp(x, MinWidth, MaxWidth)}px`;
      // lineRef.current.style.transform = `translate(${state.offset[0]}px, ${state.offset[1]}px)`;
    },
    { from: width }
  );

  // 2. drag and dragEnd
  // const [position, setPosition] = useState({ x: 0, y: 0 });
  // const bind = useGesture(
  //   {
  //     onDrag: (state) => {
  //       if (!lineRef.current) return;
  //       lineRef.current.style.transform = `translate(${state.offset[0]}px, ${state.offset[1]}px)`;
  //     },
  //     onDragEnd: ({ offset: [mx, my] }) => {
  //       setPosition({
  //         x: mx,
  //         y: my,
  //       });
  //     },
  //   },
  //   {}
  // );

  // 3. body Scroll Lock

  return (
    <div
      className='relative border inline-flex h-[calc(100%-100px)] my-2'
      style={{ width }}
      ref={containerRef}
    >
      <div
        {...bind()}
        ref={lineRef}
        className='w-2 bg-black absolute h-[calc(100%-20px)] top-2.5 -right-1'
        // style={{ transform: `translate(${position.x}px, ${position.y}px)` }}
      ></div>
    </div>
  );
};

// offset,        // offset since the first gesture
// lastOffset,    // offset when the last gesture started
// movement,      // displacement between offset and lastOffset
