import { FC, useEffect, useState } from 'react';
import { NavLink } from 'react-router-dom';

interface DirectoryProps {
  setFileUrl: React.Dispatch<React.SetStateAction<string>>;
}

interface Dir {
  dir: string;
  files: (string | Dir)[];
}
export const Directory: FC<DirectoryProps> = ({ setFileUrl }) => {
  const [dirFiles, setDirFiles] = useState<(string | Dir)[]>([]);

  useEffect(() => {
    setDirFiles([
      'mongo.md',
      'other.md',
      'regexp.md',
      'responsive.md',
      'serverless.md',
      'swaggeer.md',
      { dir: 'test', files: ['test.md'] },
      'todo.md',
      'vercel.md',
      '免密登录.md',
    ]);

    // fetch('/api/articles')
    //   .then((res) => res.json())
    //   .then((files) => {
    //     setDirFiles(files);
    //   });
  }, []);

  const renderTree = (files: any, deep = 1, root = '') => {
    return (
      <div
        key={root}
        className='flex flex-col'
        style={{
          paddingLeft: (deep - 1) * 20,
        }}
      >
        {files.map((fileName: any) => {
          if (typeof fileName === 'string') {
            const realName = fileName.split('.md')[0];
            const filePath = root + '/' + fileName;
            return (
              <NavLink
                className='h-7 p-2 flex items-center rounded'
                key={fileName}
                to={`/note${filePath.replace('.md', '')}`}
                activeClassName='bg-[#37352f14]'
                onClick={() => setFileUrl(filePath)}
              >
                {realName}
              </NavLink>
            );
          } else {
            return (
              <div key={fileName.dir} className='p-2'>
                <span className='h-7 flex items-center rounded'>{fileName.dir}</span>
                {renderTree(fileName.files, deep + 1, root + '/' + fileName.dir)}
              </div>
            );
          }
        })}
      </div>
    );
  };

  return (
    <div className='w-[120px] min-w-[120px] border-r text-md p-2 text-sm'>
      {renderTree(dirFiles)}
    </div>
  );
};
