import { v4 } from 'uuid';
import faker from '@faker-js/faker';

export const generateData = (len: number) => {
  return new Array(len).fill(1).map((value, index) => {
    return {
      id: v4(),
      index: index + 1,
      content: faker.lorem.sentences(),
    };
  });
};

interface Data {
  id: string;
  index: number;
  content: string;
}

export const generateMapData = (len: number) => {
  const dataMap: Record<string, Data> = {};
  const idList: string[] = [];
  const dataList: Data[] = [];

  new Array(len).fill(1).map((value, index) => {
    const id = v4();
    const data = {
      id,
      index: index + 1,
      content: faker.lorem.sentences(),
    };

    dataList.push(data);
    idList.push(id);
    dataMap[id] = data;
  });

  return { idList, dataList, dataMap };
};
