/**
 * 什么时候使用重载函数？什么时候用 union type。
 * 函数重载将特定的输入类型映射到特定的返回类型。使用联合，则只知道返回是有效类型之一，失去了输入和输出之间的关联。
 * 这可能是也可能不是问题，看情况而论
 *
 * implementation signature和 overload signature必须兼容，
 *
 * js 因为是动态类型，本身不需要支持重载，直接对参数进行类型判断即可，但是ts为了保证类型安全，支持了函数签名的类型重载
 *
 * https://stackoverflow.com/questions/64741234/function-overloading-in-typescript-vs-using-a-union-type
 * @param type
 */
export function getPersonInfo(type: 'age'): number;
export function getPersonInfo(type: 'name'): string;
export function getPersonInfo(type: 'age' | 'name'): number | string {
  if (type === 'age') {
    return 12;
  } else {
    return '111';
  }
}
