import { useEffect } from 'react';

export const usePerformance = () => {
  useEffect(() => {
    const callback: PerformanceObserverCallback = (list) => {
      for (const entry of list.getEntries()) {
        console.groupCollapsed(entry.name);
        console.log('entryType', entry.entryType);
        console.log('startTime', entry.startTime);
        console.log('duration', entry.duration);
        console.groupEnd();
      }
    };
    const observer = new PerformanceObserver(callback);
    observer.observe({
      entryTypes: ['navigation', 'resource', 'mark', 'measure', 'paint'],
    });
  }, []);

  /**
   * 进入路由 http://localhost:3001/example/memory，点击隐藏 button，显示侧边栏，此时才会 console 出 first-contentful-paint。
   * 也就是说 first-contentful-paint 的触发时机是页面上至少有可见元素(比如文字，图片)才行
   */

  return null;
};
