const resourceMap: Record<string, Promise<void> | undefined> = {};

export enum TagName {
  SCRIPT = 'script',
  LINK = 'link',
}

export const loadResource = (url: string, tagName: TagName) => {
  if (resourceMap[url]) return resourceMap[url];

  resourceMap[url] = new Promise((resolve, reject) => {
    let tag: HTMLScriptElement | HTMLLinkElement;
    if (tagName === 'script') {
      tag = document.createElement(tagName) as HTMLScriptElement;
      tag.src = url;
    } else {
      tag = document.createElement(tagName) as HTMLLinkElement;
      tag.href = url;
      tag.rel = 'stylesheet';
      tag.type = 'text/css';
    }

    tag.onload = () => resolve();
    tag.onerror = (event) => reject(event);
    document.head.appendChild(tag);
  });

  return resourceMap[url];
};

export const loadJs = (url: string) => loadResource(url, TagName.SCRIPT);
export const loadCss = (url: string) => loadResource(url, TagName.LINK);
