import { FC, useEffect, useState } from 'react';
import { Route, Switch } from 'react-router-dom';
import Example from '../example';
import { getDateTimeStamp } from '../utils/dates';
import { TodayContext } from './context';
import Loadable from 'react-loadable';
import Docs from '../docs';

const SwaggerCom = Loadable({
  loader: () => import('../example/swagger'),
  loading: () => <div>loading</div>,
});

// const Docs = Loadable({
//   loader: () => import('../docs/index'),
//   loading: () => <div>loading</div>,
// });

export const Main: FC = () => {
  const [today, setToday] = useState(getDateTimeStamp(Date.now()));

  useEffect(() => {
    setInterval(() => {
      if (today !== getDateTimeStamp(Date.now())) {
        setToday(getDateTimeStamp(Date.now()));
      }
    }, 30 * 60 * 1000);
  }, [today]);

  return (
    <TodayContext.Provider value={today}>
      <Switch>
        <Route path='/swagger' component={SwaggerCom}></Route>
        <Route path='/note' component={Docs}></Route>
        <Route path='/' component={Example}></Route>
      </Switch>
    </TodayContext.Provider>
  );
};

