import { arrayMove } from '@dnd-kit/sortable';
import dayjs from 'dayjs';
import { debounce, isEqual, last, throttle } from 'lodash-es';
import React, {
  createContext,
  FC,
  forwardRef,
  memo,
  useCallback,
  useContext,
  useEffect,
  useLayoutEffect,
  useMemo,
  useRef,
  useState,
} from 'react';
import { BsArrowLeftShort, BsArrowRightShort } from 'react-icons/bs';
import { MdKeyboardArrowLeft, MdKeyboardArrowRight } from 'react-icons/md';
import { v4 } from 'uuid';
import { useToday } from '../main/context';
import { autoScrollX, autoScrollY, rafX, rafY } from '../utils/auto-scroll';
import { cx } from '../utils/classnames';
import {
  DateDir,
  formatDate,
  getDateRange,
  getDateTimeStamp,
  getTimelineDateRange,
  ShowType,
} from '../utils/dates';
import { getIntersectionRatio } from '../utils/intersection-ratio';

export const DATE_TIME_FORMAT = 'YYYY/MM/DD HH:mm';
export const DATE_FORMAT = 'YYYY/MM/DD';
export const NewCardPlaceholderId = 'NewRecordPlaceholderId';
export const IS_IPHONE = typeof navigator !== 'undefined' && /iPhone/i.test(navigator.userAgent);
export const DateSteppingLength = 100;
export const OneDayMillisecond = 24 * 60 * 60 * 1000;
export const DateElementWidth = 40;
export const RecordLineHeight = 40;
const ScrollBufferLength = 300;

interface RecordDataContext {
  records: RecordData[];
  setRecords: React.Dispatch<React.SetStateAction<RecordData[]>>;
}
const RecordDataContext = createContext<RecordDataContext | undefined>(undefined);
const useRecordData = () => {
  const context = useContext(RecordDataContext);
  if (!context) {
    throw new Error('RecordDataContext 不存在');
  }
  return context;
};

interface TimelineContext {
  showType: ShowType;
  setShowType: React.Dispatch<React.SetStateAction<ShowType>>;
  timelineDates: number[];
  setTimelineDates: React.Dispatch<React.SetStateAction<number[]>>;
  dateStepLength: React.MutableRefObject<number>;
  timelineDatesRef: React.MutableRefObject<number[]>;
  container: React.MutableRefObject<HTMLDivElement | null>;
  scrollContainer: React.MutableRefObject<HTMLDivElement | null>;
  containerScrollLeft: React.MutableRefObject<number | undefined>;
  draggingRecordId: React.MutableRefObject<string | undefined>;
  datesContainer: React.MutableRefObject<HTMLDivElement | null>;
  timelineHeader: React.MutableRefObject<HTMLDivElement | null>;
  monthContainer: React.MutableRefObject<HTMLDivElement | null>;
  listRef: React.MutableRefObject<HTMLDivElement | null>;
}

export const TimelineContext = createContext<TimelineContext | undefined>(undefined);

export const useTimeline = () => {
  const context = useContext(TimelineContext);
  if (!context) {
    throw new Error('useBitable');
  }
  return context;
};

export const useRecord = (uuid: string) => {
  const { records } = useRecordData();
  return records.find((record) => record.id === uuid);
};

export const TimeLineView: FC = () => {
  const [showType, setShowType] = useState(ShowType.MONTH);
  const [timelineDates, setTimelineDates] = useState<number[]>(getTimelineDateRange({ showType }));
  const container = useRef<HTMLDivElement | null>(null);
  const scrollContainer = useRef<HTMLDivElement | null>(null);
  const containerScrollLeft = useRef<number | undefined>();
  const dateStepLength = useRef<number>(100);
  const timelineDatesRef = useRef<number[]>(timelineDates);
  const draggingRecordId = useRef<string | undefined>();
  const datesContainer = useRef<HTMLDivElement>(null);
  const timelineHeader = useRef<HTMLDivElement>(null);
  const monthContainer = useRef<HTMLDivElement>(null);
  const listRef = useRef<HTMLDivElement>(null);
  const [records, setRecords] = useState<RecordData[]>(
    new Array(10).fill(1).map((_, index) => {
      return {
        id: String(index + 1),
        recordStartTime: getDateTimeStamp(Date.now()) + OneDayMillisecond,
        recordEndTime: getDateTimeStamp(Date.now()) + OneDayMillisecond * 10,
      };
    })
  );

  const context = useMemo(() => {
    return {
      showType,
      setShowType,
      timelineDatesRef,
      dateStepLength,
      timelineDates,
      setTimelineDates,
      container,
      scrollContainer,
      containerScrollLeft,
      datesContainer,
      timelineHeader,
      monthContainer,
      listRef,
      draggingRecordId,
    };
  }, [showType, timelineDates]);

  const recordData = useMemo(() => {
    return {
      records,
      setRecords,
    };
  }, [records]);
  // react dependency eslint
  timelineDatesRef.current = timelineDates;
  return (
    <TimelineContext.Provider value={context}>
      <RecordDataContext.Provider value={recordData}>
        <TimeLine />
      </RecordDataContext.Provider>
    </TimelineContext.Provider>
  );
};

export const TimeLine: FC = () => {
  const {
    showType,
    timelineDates,
    setTimelineDates,
    container,
    scrollContainer,
    datesContainer,
    timelineHeader,
    monthContainer,
    containerScrollLeft,
    listRef,
    dateStepLength,
  } = useTimeline();
  const { records } = useRecordData();
  const [range, setRange] = useState([
    0,
    Math.min(window.innerHeight / RecordLineHeight, records.length),
  ]);

  useLayoutEffect(() => {
    if (!container.current) return;
    container.current.scrollLeft =
      DateElementWidth * dateStepLength.current - container.current.clientWidth / 2;
  }, [container, dateStepLength]);

  useLayoutEffect(() => {
    if (!container.current || !containerScrollLeft.current) return;

    container.current.scrollLeft = containerScrollLeft.current;
    containerScrollLeft.current = undefined;
  }, [timelineDates, container, containerScrollLeft]);

  useEffect(() => {
    if (!container.current) return;

    // 处理当前屏幕超宽导致 range 的展示长度不够,需要修改步进长度
    if (container.current.scrollWidth < container.current.clientWidth * 2) {
      dateStepLength.current = Math.ceil((container.current.clientWidth * 2) / DateElementWidth);
      setTimelineDates(
        getTimelineDateRange({
          showType,
          length: dateStepLength.current,
        })
      );
      containerScrollLeft.current =
        DateElementWidth * dateStepLength.current - container.current.clientWidth / 2;
    }

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [container.current?.clientWidth]);

  useEffect(() => {
    if (!container.current || !datesContainer.current) return;
    const containerNode = container.current;

    const handleScroll = () => {
      if (typeof containerScrollLeft.current !== 'undefined' || !container.current) return;

      if (
        containerNode.scrollWidth - containerNode.scrollLeft - containerNode.clientWidth <
        ScrollBufferLength
      ) {
        containerScrollLeft.current =
          container.current.scrollLeft - dateStepLength.current * DateElementWidth;
        setTimelineDates((dates) => {
          const lastDay = last(dates) as number;
          const appendDates = getDateRange({
            showType,
            base: lastDay,
            length: dateStepLength.current,
            dir: DateDir.NEXT,
          });
          const newDates = [...dates.slice(dateStepLength.current), ...appendDates];
          return newDates;
        });
      }

      if (containerNode.scrollLeft < ScrollBufferLength) {
        containerScrollLeft.current =
          container.current.scrollLeft + dateStepLength.current * DateElementWidth;
        setTimelineDates((dates) => {
          const firstDay = dates[0] as number;
          const appendDates = getDateRange({
            showType,
            base: firstDay,
            length: dateStepLength.current,
            dir: DateDir.PREV,
          });
          const newDates = [
            ...appendDates,
            ...dates.slice(0, dates.length - dateStepLength.current),
          ];
          return newDates;
        });
      }
    };

    const scrollFn = IS_IPHONE ? debounce(handleScroll) : throttle(handleScroll, 60);
    containerNode.addEventListener('scroll', scrollFn);
    return () => containerNode.removeEventListener('scroll', scrollFn);
  }, [container, containerScrollLeft, dateStepLength, datesContainer, setTimelineDates, showType]);

  useEffect(() => {
    const containerNode = container.current;
    const datesContainerNode = datesContainer.current;
    const timelineHeaderNode = timelineHeader.current;
    const monthContainerNode = monthContainer.current;
    const scrollContainerNode = scrollContainer.current;
    if (
      !containerNode ||
      !datesContainerNode ||
      !timelineHeaderNode ||
      !monthContainerNode ||
      !scrollContainerNode
    ) {
      return;
    }

    const handleScroll = () => {
      const containerRect = containerNode.getBoundingClientRect();
      const scrollContainerRect = scrollContainerNode.getBoundingClientRect();

      const translateY = Math.abs(scrollContainerRect.top - containerRect.top);
      if (translateY > 0) {
        timelineHeaderNode.style.transform = `translate3d(0,${Math.round(translateY)}px,0)`;
        monthContainerNode.style.transform = `translate3d(0,${Math.round(translateY)}px,0)`;
        datesContainerNode.style.transform = `translate3d(0,${Math.round(translateY)}px,0)`;
      } else {
        timelineHeaderNode.style.transform = 'none';
        monthContainerNode.style.transform = 'none';
        datesContainerNode.style.transform = 'none';
      }
    };

    scrollContainerNode.addEventListener('scroll', handleScroll);
    window.addEventListener('resize', handleScroll);
    return () => {
      scrollContainerNode.removeEventListener('scroll', handleScroll);
      window.removeEventListener('resize', handleScroll);
    };
  }, [container, datesContainer, monthContainer, scrollContainer, timelineHeader]);

  useEffect(() => {
    const scrollContainerNode = scrollContainer.current;
    const listNode = listRef.current;
    if (!scrollContainerNode || !listNode) return;

    const handleScroll = () => {
      const listRect = listNode.getBoundingClientRect();
      const containerRect = scrollContainerNode.getBoundingClientRect();
      const visibleLength = Math.ceil(scrollContainerNode.clientHeight / RecordLineHeight) + 10;
      const hiddenLength = containerRect.top - listRect.top;
      const startIndex = Math.max(Math.floor(hiddenLength / RecordLineHeight) - 5, 0);
      setRange([startIndex, startIndex + visibleLength]);
    };

    handleScroll();
    scrollContainerNode.addEventListener('scroll', handleScroll);
    return () => {
      scrollContainerNode.removeEventListener('scroll', handleScroll);
    };
  }, [listRef, scrollContainer]);

  return (
    <div
      className='relative w-full timeline-page overflow-scroll'
      style={{ height: 'calc(100% - 40px)' }}
      ref={scrollContainer}
    >
      <Header ref={timelineHeader} />

      <div className='w-full overflow-auto relative pb-[40px]' ref={container}>
        <TimelineMonths ref={monthContainer} />
        <TimelineDates />
        <TimelineWeekend />

        <div
          className='record-line-container relative'
          ref={listRef}
          style={{
            width: timelineDates.length * DateElementWidth,
            height: records.length * RecordLineHeight + RecordLineHeight,
          }}
        >
          {records.slice(range[0], range[1]).map((record, index) => (
            <RecordLine key={record.id} recordIndex={range[0] + index} {...record} />
          ))}

          <RecordLine id={NewCardPlaceholderId} recordIndex={records.length} />
        </div>
      </div>
    </div>
  );
};

const Header = memo(
  forwardRef<HTMLDivElement>((_, ref) => {
    const {
      timelineDates,
      container,
      setTimelineDates,
      dateStepLength,
      containerScrollLeft,
      showType,
      setShowType,
    } = useTimeline();
    const [currentMonth, setCurrentMonth] = useState<number>();

    useEffect(() => {
      const containerNode = container.current;
      if (!containerNode) return;

      const handleScroll = () => {
        if (!container.current) return;
        const dateLength = Math.ceil(container.current.scrollLeft / DateElementWidth);
        const date = timelineDates[dateLength - 1];

        if (date) {
          setCurrentMonth((currentMonth) => {
            const newMonth = new Date(date).setDate(1);
            if (newMonth !== currentMonth) {
              return newMonth;
            }
            return currentMonth;
          });
        }
      };
      handleScroll();
      containerNode.addEventListener('scroll', handleScroll);
      return () => containerNode.removeEventListener('scroll', handleScroll);
    }, [container, timelineDates]);

    return (
      <div className='absolute left-0 right-0 flex items-center z-50 h-8 top-1' ref={ref}>
        <div className='h-full flex'>
          {currentMonth && (
            <span className='bg-white h-full inline-flex items-center'>
              {formatDate(currentMonth).year}年{formatDate(currentMonth).month}月
            </span>
          )}

          <span
            className='w-10 h-full'
            style={{
              backgroundImage: 'linear-gradient(to right, white 20%, rgba(255, 255, 255, 0) 100%)',
            }}
          />
        </div>
        <div className='ml-auto flex items-center'>
          <span
            className='w-10 h-8 inline-block'
            style={{
              backgroundImage: 'linear-gradient(to left, white 20%, rgba(255, 255, 255, 0) 100%)',
            }}
          />

          <div className='bg-white flex items-center'>
            <button
              onClick={() => {
                if (!container.current) return;

                setShowType(ShowType.YEAR);
                setTimelineDates(
                  getTimelineDateRange({
                    showType: ShowType.YEAR,
                  })
                );

                containerScrollLeft.current =
                  DateElementWidth * dateStepLength.current - container.current.clientWidth / 2;
              }}
            >
              年
            </button>

            <button
              onClick={() => {
                if (!container.current) return;

                setShowType(ShowType.MONTH);
                setTimelineDates(
                  getTimelineDateRange({
                    showType: ShowType.MONTH,
                  })
                );
                containerScrollLeft.current =
                  DateElementWidth * dateStepLength.current - container.current.clientWidth / 2;
              }}
            >
              月
            </button>
            <button
              onClick={() => {
                if (container.current) {
                  container.current.scrollLeft -= 15 * DateElementWidth;
                }
              }}
            >
              <MdKeyboardArrowLeft className='w-5 h-5' />
            </button>
            <button
              className='bg-white px-2 cursor-pointer'
              onClick={() => {
                if (!container.current) return;

                setTimelineDates(
                  getTimelineDateRange({
                    showType,
                    length: dateStepLength.current,
                  })
                );
                containerScrollLeft.current =
                  DateElementWidth * dateStepLength.current - container.current.clientWidth / 2;
              }}
            >
              今天
            </button>
            <button
              onClick={() => {
                if (container.current) {
                  container.current.scrollLeft += 15 * DateElementWidth;
                }
              }}
            >
              <MdKeyboardArrowRight className='w-5 h-5' />
            </button>
          </div>
        </div>
      </div>
    );
  }),
  isEqual
);

export interface RecordData {
  id: string;
  recordStartTime?: number;
  recordEndTime?: number;
}

// #region 三个基础组件
export const TimelineMonths = memo(
  forwardRef<HTMLDivElement>((_, ref) => {
    const { timelineDates, showType } = useTimeline();
    return (
      <div
        className='timeline-month relative z-10 h-[38px] bg-white text-t2-medium border-t'
        style={{ width: timelineDates.length * DateElementWidth }}
        ref={ref}
      >
        {timelineDates.map((date, index) => {
          let shouldRender = false;
          if (showType === ShowType.MONTH) {
            shouldRender = new Date(date).getDate() === 1;
          } else if (showType === ShowType.YEAR) {
            const monthFirstDate = new Date(new Date(date).setDate(1));
            const monthFirstSunday =
              monthFirstDate.getTime() + (7 - monthFirstDate.getDay() || 7) * OneDayMillisecond;
            shouldRender = monthFirstSunday === date;
          }

          if (shouldRender) {
            return (
              <div
                key={date}
                style={{ left: index * DateElementWidth }}
                className='absolute h-full inline-flex items-center justify-center flex-shrink-0'
              >
                {new Date(date).getMonth() + 1}月
              </div>
            );
          }

          return null;
        })}
      </div>
    );
  }),
  isEqual
);

export const TimelineDates = memo(() => {
  const { timelineDates, datesContainer, container, showType } = useTimeline();
  const today = useToday();
  const [range, setRange] = useState([
    0,
    Math.max(Math.ceil(window.innerWidth / DateElementWidth), timelineDates.length),
  ]);

  useEffect(() => {
    const scrollContainerNode = container.current;
    const datesContainerNode = datesContainer.current;
    if (!scrollContainerNode || !datesContainerNode) return;

    const handleScroll = () => {
      const containerRect = scrollContainerNode.getBoundingClientRect();
      const listRect = datesContainerNode.getBoundingClientRect();
      const visibleLength = Math.ceil(scrollContainerNode.clientWidth / DateElementWidth) + 10;
      const hiddenLength = containerRect.left - listRect.left;
      const startIndex = Math.max(Math.floor(hiddenLength / DateElementWidth) - 5, 0);
      setRange([startIndex, startIndex + visibleLength]);
    };

    handleScroll();
    scrollContainerNode.addEventListener('scroll', handleScroll);
    return () => scrollContainerNode.removeEventListener('scroll', handleScroll);
  }, [datesContainer, container]);

  const renderToday = () => {
    const firstDate = timelineDates[0];
    const dateStep = showType === ShowType.MONTH ? OneDayMillisecond : OneDayMillisecond * 7;
    const offset = (today - firstDate) / dateStep;

    return (
      <div
        className='w-6 h-6 absolute inline-flex justify-center items-center rounded-full bg-green-400 text-white'
        style={{
          left: offset * DateElementWidth + 8,
        }}
      >
        {new Date().getDate()}
        <span className='absolute top-[20px] left-3 h-screen w-[1px] bg-green-400' />
      </div>
    );
  };

  const rangeStart = range[0];
  const rangeEnd = range[1];
  const notRenderDates = [today];
  if (showType === ShowType.YEAR) {
    const weekday = new Date().getDay();
    const nextSunday = today + (7 - (weekday || 7)) * OneDayMillisecond;
    const prevSunday = today - weekday * OneDayMillisecond;
    notRenderDates.push(prevSunday, nextSunday);
  }
  return (
    <div
      ref={datesContainer}
      className='timeline-weekdate relative z-10 h-[38px] py-1.5 bg-white border-b font-medium text-grey4 text-t2-medium'
      style={{ width: timelineDates.length * DateElementWidth }}
    >
      {timelineDates.slice(rangeStart, rangeEnd).map((date, index) => {
        if (notRenderDates.includes(date)) return null;

        return (
          <div
            key={date}
            className='w-10 h-6  border inline-flex items-center justify-center absolute'
            style={{
              left: (rangeStart + index) * DateElementWidth,
            }}
          >
            {formatDate(date).date}
          </div>
        );
      })}

      {renderToday()}
    </div>
  );
}, isEqual);

export const TimelineWeekend = memo(() => {
  const { timelineDates, showType } = useTimeline();

  if (showType === ShowType.MONTH) {
    let backgroundLeft = 0;
    const firstDate = timelineDates[0] as number;
    const firstDay = new Date(firstDate).getDay();
    if (firstDay === 0) {
      backgroundLeft = -DateElementWidth;
    } else if (firstDay) {
      backgroundLeft = (6 - firstDay) * DateElementWidth;
    }
    return (
      <div
        className='timeline-weekend absolute top-0 h-full'
        style={{
          width: timelineDates.length * DateElementWidth,
          background: `repeating-linear-gradient(90deg, rgb(229,231,235) 0px,rgb(229,231,235) ${
            DateElementWidth * 2
          }px, transparent ${DateElementWidth * 2}px,  transparent ${DateElementWidth * 7}px)`,
          backgroundPositionX: `${backgroundLeft}px`,
        }}
      />
    );
  } else {
    return (
      <div
        className='timeline-weekend absolute top-0 h-full'
        style={{
          width: timelineDates.length * DateElementWidth,
        }}
      >
        {timelineDates.map((date) => {
          const monthFirstDate = new Date(new Date(date).setDate(1));
          const monthFirstSunday =
            monthFirstDate.getTime() + (7 - monthFirstDate.getDay() || 7) * OneDayMillisecond;
          const shouldRender = monthFirstSunday === date;

          if (shouldRender) {
            const firstDate = timelineDates[0];
            const dateStep = OneDayMillisecond * 7;
            const offset = (monthFirstDate.getTime() - firstDate) / dateStep;

            return (
              <div
                key={date}
                style={{ left: offset * DateElementWidth + DateElementWidth / 2 }}
                className='absolute h-full bg-gray-400 w-[1px]'
              />
            );
          }

          return null;
        })}
      </div>
    );
  }
}, isEqual);
// #endregion

export const RecordLine: FC<RecordData & { recordIndex: number }> = memo((props) => {
  const { id, recordEndTime, recordStartTime, recordIndex } = props;
  const { setRecords } = useRecordData();
  const { timelineDates, container, datesContainer, draggingRecordId } = useTimeline();
  const cardRef = useRef<HTMLDivElement | null>(null);
  const [movingStartDateIndex, setMovingStartDateIndex] = useState<number>();
  const initDateRangeLength = 5;

  const handleMouseMove = useCallback(
    (event: React.MouseEvent) => {
      if (recordStartTime && recordEndTime) return;

      if (draggingRecordId.current) return;
      if (!container.current) return;

      const rect = container.current.getBoundingClientRect();
      const leftDistance = container.current.scrollLeft + event.clientX - rect.x;
      const dateLength = Math.floor(leftDistance / DateElementWidth);
      setMovingStartDateIndex(dateLength);

      removeWeekdateHightlight(container.current);
      addWeekdateHightlight(dateLength, initDateRangeLength, datesContainer.current);
    },
    [container, datesContainer, draggingRecordId, recordEndTime, recordStartTime]
  );

  const handleMouseLeave = useCallback(() => {
    setMovingStartDateIndex(undefined);
    removeWeekdateHightlight(container.current);
  }, [container]);

  const handleMouseClick = useCallback(() => {
    if (movingStartDateIndex) {
      const startTime = timelineDates[movingStartDateIndex] as number;
      const endTime = startTime + (initDateRangeLength - 1) * OneDayMillisecond;

      if (id === NewCardPlaceholderId) {
        setRecords((records) => records.concat({ id: v4() }));
      } else {
        setRecords((records) => {
          return records.map((record) => {
            if (record.id === id) {
              return {
                id,
                recordStartTime: startTime,
                recordEndTime: endTime,
              };
            }

            return record;
          });
        });
      }

      setMovingStartDateIndex(undefined);
      removeWeekdateHightlight(container.current);
    }
  }, [container, id, movingStartDateIndex, setRecords, timelineDates]);

  const hasValidDateRange = recordStartTime && recordEndTime;

  return useMemo(() => {
    return (
      <div
        data-disable-select
        data-record-id={id}
        onMouseMove={handleMouseMove}
        onMouseLeave={handleMouseLeave}
        onClick={handleMouseClick}
        className={cx('absolute w-full flex h-10', !hasValidDateRange && 'cursor-pointer')}
        style={{ top: recordIndex * RecordLineHeight }}
      >
        {movingStartDateIndex && (
          <PlaceholderCard
            recordId={id}
            initDateRangeLength={initDateRangeLength}
            movingStartDateIndex={movingStartDateIndex}
          />
        )}

        {hasValidDateRange && (
          <>
            <IndicatorArrow recordId={id} card={cardRef} />
            <RecordCard
              id={id}
              recordEndTime={recordEndTime}
              recordStartTime={recordStartTime}
              card={cardRef}
            />
          </>
        )}
      </div>
    );
  }, [
    id,
    handleMouseMove,
    handleMouseLeave,
    handleMouseClick,
    hasValidDateRange,
    recordIndex,
    movingStartDateIndex,
    recordEndTime,
    recordStartTime,
  ]);
}, isEqual);

//#region IndicatorArrow PlaceholderCard RecordCard 三个组件
export interface ArrowShowStatus {
  left: boolean;
  right: boolean;
}
export enum Direction {
  left = 'left',
  right = 'right',
}
export const DirectionMap = [Direction.left, Direction.right];
interface IndicatorArrowProps {
  recordId: string;
  card: React.MutableRefObject<HTMLDivElement | null>;
}

export const IndicatorArrow: FC<IndicatorArrowProps> = memo(({ recordId, card }) => {
  const { records } = useRecordData();
  const { showType, setTimelineDates, container, containerScrollLeft, dateStepLength } =
    useTimeline();
  const [indicatorArrow, setIndicatorArrow] = useState<ArrowShowStatus>({
    left: false,
    right: false,
  });
  const recordData = records.find((record) => record.id === recordId);

  const handleScroll = useCallback(() => {
    if (!container.current || !card.current) return;

    const cardRect = card.current.getBoundingClientRect();
    const containerRect = container.current.getBoundingClientRect();

    if (
      indicatorArrow.left !== cardRect.left < containerRect.left ||
      indicatorArrow.right !== cardRect.right > containerRect.right
    ) {
      setIndicatorArrow({
        left: cardRect.left < containerRect.left,
        right: cardRect.right > containerRect.right,
      });
    }
  }, [card, container, indicatorArrow.left, indicatorArrow.right]);

  useEffect(() => {
    if (!container.current) return;
    const containerNode = container.current;

    containerNode.addEventListener('scroll', handleScroll);
    return () => containerNode.removeEventListener('scroll', handleScroll);
  }, [container, handleScroll]);

  useEffect(() => {
    handleScroll();
  }, [handleScroll, container.current?.clientWidth]);

  if (!recordData) return null;

  const { recordStartTime, recordEndTime } = recordData;
  if (!recordStartTime || !recordEndTime) return null;

  const handleClick = (direction: Direction) => {
    if (!container.current) return;

    const edgeOffset = 100;
    const isLeftDirection = direction === Direction.left;

    if (isLeftDirection) {
      containerScrollLeft.current = dateStepLength.current * DateElementWidth - edgeOffset;
    } else {
      containerScrollLeft.current =
        (dateStepLength.current + 1) * DateElementWidth -
        (container.current.clientWidth - edgeOffset);
    }

    setTimelineDates(
      getTimelineDateRange({
        showType,
        base: isLeftDirection ? recordStartTime : recordEndTime,
        length: dateStepLength.current,
      })
    );
    setIndicatorArrow({
      left: isLeftDirection ? false : indicatorArrow.left,
      right: isLeftDirection ? indicatorArrow.right : false,
    });
  };

  return (
    <>
      {DirectionMap.map((direction) => {
        if (!container.current) return null;

        const isRightDirection = direction === Direction.right;
        const isShow = isRightDirection ? indicatorArrow.right : indicatorArrow.left;

        return (
          <button
            key={direction}
            onClick={() => handleClick(direction)}
            className='sticky h-full rounded cursor-pointer duration-200 transition-all flex items-center group z-[5]'
            style={{
              left: isRightDirection ? container.current.clientWidth - 32 : 0,
              opacity: isShow ? 1 : 0,
              transform: isShow ? 'scale(1)' : 'scale(0)',
            }}
          >
            <BsArrowLeftShort
              className={cx('text-white bg-black/30 rounded', isRightDirection && 'rotate-180')}
            />

            <span
              className={cx(
                'absolute px-1 flex items-center text-black bg-white border rounded text-t4 opacity-0 group-hover:opacity-100 pointer-events-none',
                isRightDirection ? 'right-6' : 'left-6'
              )}
            >
              {dayjs(recordStartTime).format(DATE_FORMAT)}
              <BsArrowRightShort />
              {dayjs(Math.max(recordStartTime, recordEndTime)).format(DATE_FORMAT)}
            </span>
          </button>
        );
      })}
    </>
  );
}, isEqual);

interface PlaceholderCardProps {
  recordId: string;
  initDateRangeLength: number;
  movingStartDateIndex: number;
}
export const PlaceholderCard: FC<PlaceholderCardProps> = ({
  initDateRangeLength,
  movingStartDateIndex,
  recordId,
}) => {
  return (
    <div
      className='absolute h-[34px] top-0.5 bg-green-100 rounded pointer-events-none px-1.5 flex items-center whitespace-nowrap'
      style={{
        width: DateElementWidth * initDateRangeLength,
        left: movingStartDateIndex * DateElementWidth,
      }}
    >
      <span>+</span>
      <span className='text-t2 ml-2'>
        {recordId === NewCardPlaceholderId ? '新建' : '新建事项'}
      </span>
    </div>
  );
};

interface RecordCardProps {
  id: string;
  recordEndTime: number;
  recordStartTime: number;
  card: React.MutableRefObject<HTMLDivElement | null>;
}

export const RecordCard: FC<RecordCardProps> = memo(
  ({ id, recordEndTime, recordStartTime, card }) => {
    const {
      showType,
      container,
      datesContainer,
      timelineDates,
      timelineDatesRef,
      draggingRecordId,
      scrollContainer,
    } = useTimeline();
    const { setRecords } = useRecordData();
    const [draggingNewDate, setDraggingNewDate] = useState<number>();
    const [draggingDirection, setDraggingDirection] = useState<Direction>();
    const mouseDownPos = useRef({ x: 0, y: 0 });

    useLayoutEffect(() => {
      if (draggingNewDate && card.current) {
        const cardStyle = getComputedStyle(card.current);
        const left = parseInt(cardStyle.getPropertyValue('left'), 10);
        const width = parseInt(cardStyle.getPropertyValue('width'), 10);
        const dateIndex = Math.floor(left / DateElementWidth);

        removeWeekdateHightlight(container.current);
        addWeekdateHightlight(dateIndex, width / DateElementWidth, datesContainer.current);
      }
    }, [card, container, datesContainer, draggingNewDate]);

    if (!recordStartTime || !recordEndTime) return null;

    const handleTranslate = (event: React.MouseEvent) => {
      event.preventDefault();
      mouseDownPos.current = {
        x: event.clientX,
        y: event.clientY,
      };

      const containerNode = container.current;
      const cardNode = card.current;
      const timelinePage = scrollContainer.current;
      if (!containerNode || !cardNode || !timelinePage) return;
      const cardStyle = getComputedStyle(cardNode);
      const cardWidth = parseInt(cardStyle.getPropertyValue('width'), 10);
      const cardRect = (event.currentTarget as HTMLDivElement).getBoundingClientRect();
      const deltaCardX = event.clientX - cardRect.left;
      let lastCursorPos = { x: event.clientX, y: event.clientY };
      let dragStartDate: number;
      let isMoving = false;
      let overlayContainer: HTMLDivElement | undefined;
      let target: HTMLElement | undefined = undefined;

      const handleMouseMove = (event: MouseEvent) => {
        lastCursorPos = { x: event.clientX, y: event.clientY };
        if (!isMoving) {
          if (
            Math.abs(event.clientX - mouseDownPos.current.x) < 5 &&
            Math.abs(event.clientY - mouseDownPos.current.y) < 5
          ) {
            return;
          }
          isMoving = true;
          draggingRecordId.current = id;
          overlayContainer = createOverlay(cardNode);
        }

        autoScrollX(event.clientX, containerNode);
        autoScrollY({ y: event.clientY, container: timelinePage });

        const containerRect = containerNode.getBoundingClientRect();
        const leftDistance =
          containerNode.scrollLeft + event.clientX - containerRect.x - deltaCardX; // 计算 card 起始时间距离最左端的距离
        const dateIndex = Math.round(leftDistance / DateElementWidth);
        const newDate = timelineDatesRef.current[dateIndex] as number;
        dragStartDate = newDate;

        removeWeekdateHightlight(container.current);
        addWeekdateHightlight(dateIndex, cardWidth / DateElementWidth, datesContainer.current);

        if (!overlayContainer) return;
        overlayContainer.style.transform = `translate(${
          event.clientX - mouseDownPos.current.x
        }px, ${event.clientY - mouseDownPos.current.y}px)`;

        // 添加拖拽指示线
        const timelineRecordNodes = containerNode.querySelectorAll(
          '.record-line-container [data-record-id]'
        ) as NodeListOf<HTMLElement>;
        target = addIndicator(overlayContainer, timelineRecordNodes, id);
      };

      const handleMouseUp = () => {
        timelinePage.removeEventListener('scroll', handleScroll);
        document.removeEventListener('mousemove', handleMouseMove);
        document.removeEventListener('mouseup', handleMouseUp);
        cancelAnimationFrame(rafX);
        cancelAnimationFrame(rafY);
        removeWeekdateHightlight(containerNode);
        overlayContainer?.remove();
        draggingRecordId.current = undefined;
        if (target && target.lastElementChild?.classList.contains('indicator')) {
          target.lastElementChild.remove();
        }
        if (!isMoving) return;

        setRecords((records) => {
          const oldIndex = records.findIndex((record) => record.id === id);
          const newIndex = records.findIndex((record) => record.id === target?.dataset.recordId);
          const newRecords = records.map((record) => {
            if (record.id === id) {
              return {
                id,
                recordStartTime: dragStartDate,
                recordEndTime:
                  dragStartDate + (cardWidth / DateElementWidth - 1) * OneDayMillisecond,
              };
            }

            return record;
          });

          return arrayMove(newRecords, oldIndex, newIndex);
        });
      };

      const handleScroll = () => {
        const containerRect = containerNode.getBoundingClientRect();
        const leftDistance =
          containerNode.scrollLeft + lastCursorPos.x - containerRect.x - deltaCardX; // 计算 card 起始时间距离最左端的距离
        const dateIndex = Math.round(leftDistance / DateElementWidth);
        const newDate = timelineDatesRef.current[dateIndex] as number;
        dragStartDate = newDate;

        removeWeekdateHightlight(container.current);
        addWeekdateHightlight(dateIndex, width / DateElementWidth, datesContainer.current);

        if (!overlayContainer) return;
        overlayContainer.style.transform = `translate(${
          lastCursorPos.x - mouseDownPos.current.x
        }px, ${lastCursorPos.y - mouseDownPos.current.y}px)`;

        // 添加拖拽指示线
        const timelineRecordNodes = containerNode.querySelectorAll(
          '.record-line-container [data-record-id]'
        ) as NodeListOf<HTMLElement>;
        target = addIndicator(overlayContainer, timelineRecordNodes, id);
      };

      timelinePage.addEventListener('scroll', handleScroll);
      document.addEventListener('mousemove', handleMouseMove);
      document.addEventListener('mouseup', handleMouseUp);
    };

    const handleMouseDown = (event: React.MouseEvent, direction: Direction) => {
      event.preventDefault();
      event.stopPropagation();

      const containerNode = container.current;
      if (!containerNode) return;
      setDraggingDirection(direction);
      const isLeftDirection = direction === Direction.left;
      let dragEndDate: number;
      let lastCursorX = event.clientX;

      const updateDraggingNewDate = (clientX: number) => {
        const rect = containerNode.getBoundingClientRect();
        const leftDistance = containerNode.scrollLeft + clientX - rect.x;
        const dateIndex = isLeftDirection
          ? Math.round(leftDistance / DateElementWidth)
          : Math.round(leftDistance / DateElementWidth) - 1;
        const newDate = timelineDatesRef.current[dateIndex] as number;
        if (!newDate) return;

        if (
          (isLeftDirection && newDate <= recordEndTime) ||
          (!isLeftDirection && newDate >= recordStartTime)
        ) {
          dragEndDate = newDate;
          setDraggingNewDate(newDate);
        }
      };

      const handleMouseMove = (event: MouseEvent) => {
        if (!recordEndTime || !recordStartTime) return;
        lastCursorX = event.clientX;
        draggingRecordId.current = id;

        autoScrollX(event.clientX, containerNode);
        updateDraggingNewDate(event.clientX);
      };

      const handleMouseUp = () => {
        cancelAnimationFrame(rafX);
        setDraggingDirection(undefined);
        setDraggingNewDate(undefined);
        removeWeekdateHightlight(containerNode);
        draggingRecordId.current = undefined;

        if (!dragEndDate) return;
        setRecords((records) => {
          return records.map((record) => {
            if (record.id === id) {
              return {
                id,
                recordStartTime: !isLeftDirection ? record.recordStartTime : dragEndDate,
                recordEndTime: isLeftDirection ? record.recordEndTime : dragEndDate,
              };
            }

            return record;
          });
        });

        containerNode.removeEventListener('scroll', handleScroll);
        document.removeEventListener('mousemove', handleMouseMove);
        document.removeEventListener('mouseup', handleMouseUp);
      };

      const handleScroll = () => updateDraggingNewDate(lastCursorX);

      containerNode.addEventListener('scroll', handleScroll);
      document.addEventListener('mousemove', handleMouseMove);
      document.addEventListener('mouseup', handleMouseUp);
    };

    // const dateStep = showType === ShowType.MONTH ? OneDayMillisecond : OneDayMillisecond * 7;
    const oneDateWidth = showType === ShowType.MONTH ? DateElementWidth : DateElementWidth / 7;
    let left = 0;
    let width = 0;
    if (recordStartTime && recordEndTime) {
      const dayLength =
        Math.max(getDateTimeStamp(recordEndTime) - getDateTimeStamp(recordStartTime), 0) /
        OneDayMillisecond;
      width = (dayLength + 1) * oneDateWidth;
      left =
        ((getDateTimeStamp(recordStartTime) - (timelineDates[0] as number)) / OneDayMillisecond) *
        oneDateWidth;

      // 左右拖拽缩放 block date range 过程中，dates 更新时保持 block 的显示
      if (draggingDirection && draggingNewDate) {
        if (draggingDirection === Direction.left && draggingNewDate <= recordEndTime) {
          const dayLength =
            Math.max(getDateTimeStamp(recordEndTime) - getDateTimeStamp(draggingNewDate), 0) /
            OneDayMillisecond;
          width = (dayLength + 1) * oneDateWidth;

          const newDayIndex = timelineDates.findIndex((date) => date === draggingNewDate);
          left = newDayIndex * oneDateWidth;
        }

        if (draggingDirection === Direction.right && draggingNewDate >= recordStartTime) {
          const dayLength =
            Math.max(getDateTimeStamp(draggingNewDate) - getDateTimeStamp(recordStartTime), 0) /
            OneDayMillisecond;

          width = (dayLength + 1) * oneDateWidth;
        }
      }
    }

    return (
      <div className='absolute overflow-hidden left-0 top-0 w-full h-full flex items-center'>
        <div
          ref={card}
          onMouseDown={handleTranslate}
          className='absolute h-[34px] border border-black/10 rounded cursor-pointer flex items-center whitespace-nowrap bg-white'
          style={{
            left,
            width,
            boxShadow: '0px 1px 3px rgba(0, 0, 0, 0.06)',
          }}
        >
          {id}
          {DirectionMap.map((direction) => {
            return (
              <div
                key={direction}
                className={cx(
                  'absolute h-full w-[9px] px-[3px] py-1.5 rounded-full group hover:cursor-col-resize z-10',
                  draggingDirection && draggingDirection !== direction && 'pointer-events-none',
                  direction === 'left' ? '-left-1' : '-right-1'
                )}
                onMouseDown={(event) => handleMouseDown(event, direction)}
              >
                <span
                  className={cx(
                    'inline-block w-[3px] h-full rounded-full group-hover:bg-black',
                    draggingDirection === direction && 'bg-black'
                  )}
                />
                {draggingDirection === direction && draggingNewDate && (
                  <div
                    className={cx(
                      'absolute top-1.5 bg-white text-black rounded px-1 flex items-center text-t4',
                      direction === 'left' ? 'right-3' : 'left-3'
                    )}
                  >
                    {dayjs(draggingNewDate).format('MM/DD')}
                  </div>
                )}
              </div>
            );
          })}
        </div>
      </div>
    );
  },
  isEqual
);

//#endregion

//#region dom
export const addWeekdateHightlight = (
  startIndex: number,
  length: number,
  datesContainer: HTMLDivElement | null
) => {
  if (!datesContainer) return;

  const allDatesNode = datesContainer.children;
  let i = 0;
  while (i < length) {
    const classNames = ['weekdate-hightlight'];
    if (i === 0) {
      classNames.push('weekdate-hightlight-startDate');
    }
    if (i === length - 1) {
      classNames.push('weekdate-hightlight-endDate');
    }

    const node = allDatesNode[startIndex + i];
    if (node) {
      node.classList.add(...classNames);
    }
    i++;
  }
};

export const removeWeekdateHightlight = (container: HTMLDivElement | null) => {
  if (!container) return;

  const nodes = container.querySelectorAll('.weekdate-hightlight');
  nodes.forEach((node) =>
    node.classList.remove(
      'weekdate-hightlight',
      'weekdate-hightlight-startDate',
      'weekdate-hightlight-endDate'
    )
  );
};

const createOverlay = (target: HTMLElement) => {
  const cardRect = target.getBoundingClientRect();
  const node = target.cloneNode(true) as HTMLElement;
  node.style.inset = '0';
  const overlayContainer = document.createElement('div');
  overlayContainer.style.position = 'fixed';
  overlayContainer.style.width = `${cardRect.width}px`;
  overlayContainer.style.height = `${cardRect.height}px`;
  overlayContainer.style.top = `${cardRect.top}px`;
  overlayContainer.style.left = `${cardRect.left}px`;
  overlayContainer.style.opacity = '0.5';
  overlayContainer.style.zIndex = '100';
  overlayContainer.appendChild(node);
  document.body.appendChild(overlayContainer);
  return overlayContainer;
};

const addIndicator = (
  overlay: HTMLElement,
  allNodes: NodeListOf<HTMLElement>,
  currentId: string
) => {
  let target: HTMLElement | undefined;
  let maxIntersectionRatio = 0;

  const overlayRect = overlay.getBoundingClientRect();
  allNodes.forEach((node) => {
    if (node.dataset.recordId === NewCardPlaceholderId) return;
    const rect = node.getBoundingClientRect();
    if (node.lastElementChild?.classList.contains('indicator')) {
      node.lastElementChild.remove();
    }

    const intersectionRatio = getIntersectionRatio(overlayRect, rect);
    if (intersectionRatio > maxIntersectionRatio) {
      maxIntersectionRatio = intersectionRatio;
      target = node;
    }
  });

  const indicator = document.createElement('div');
  indicator.classList.add('indicator');
  indicator.style.position = 'absolute';
  indicator.style.zIndex = '10';
  indicator.style.width = '100%';
  indicator.style.height = '4px';
  indicator.style.backgroundColor = 'rgba(0, 0, 0, 0.3)';
  if (target && target.dataset.recordId !== currentId) {
    target.appendChild(indicator);
  }

  return target;
};

//#endregion

/**
 * 总结几个难点
 * 1. 无限滚动怎么实现： scrollLeft + 固定大小的时间范围
 * 2. 拖拽的时候拖到边界怎么自动滚动：到达边界时不断设置 scrollLeft 和 scrollTop，使用 requestAnimationFrame 进行更新
 * 3. 怎么实现左右拖拽和上下排序，左右平移：cloneNode 之后，随鼠标移动 transform 改变 node 位置
 * 4. dom 结构怎么组织
 * 5. 月份怎么随着滚动变化: 通过 scrollLeft 大小计算日期 index，通过 index 的到时间值
 */

/**
 * react 性能优化常见方式
 * 1. memo(component)：避免父组件的更新引起子组件不必要的更新
 * 2. 常量可以放到 context 中，不过注意使用 useMemo 包裹一下，避免常量所在的对象每次重新创建导致消费该 context 的组件更新
 * 3. item 子组件如果需要所有 items 数据，可以使用 itemsRef，items 只在组件顶层 render 使用，item 中使用 itemsRef
 * 4. 固定高度的长列表可以使用虚拟列表，节点复用
 */

/**
 * 关于 overflow
 * overlfow 默认是 visible，元素会显示在盒子外面，不会出现滚动条，此时会撑开盒子的
 * 父元素的 scrollHeight。
 * 如果想要让盒子成为一个滚动容器，那么必须设置 overflow。
 */

//  React 会在组件挂载时给 current 属性传入 DOM 元素

/**
 * 节点复用： list container hiddenLength = (container.top - list.top)
 */
