import { FC, useState } from 'react';
import { cx } from '../../utils/classnames';
import { DynamicVisualList, DynamicVisualList2 } from './dynamic-visual-list';
import { FixedHeightVisualList } from './fixed-visual-list';

/**
 * 长列表优化方案：
 * 1. 虚拟列表
 * 2. 懒加载 => 滚动到可视区域再渲染
 * @returns
 */
export const VisualList: FC = () => {
  const [fixedHeight, setIsFixedHeight] = useState(true);
  const [dataType, setDataType] = useState('array');

  return (
    <div>
      <div className='mb-5'>
        <button
          className={cx('mr-2', fixedHeight && 'bg-gray-200')}
          onClick={() => setIsFixedHeight(true)}
        >
          固定行高
        </button>
        <button
          className={cx('mr-2', !fixedHeight && dataType === 'array' && 'bg-gray-200')}
          onClick={() => {
            setIsFixedHeight(false);
            setDataType('array');
          }}
        >
          动态行高 Array
        </button>
        <button
          className={cx(!fixedHeight && dataType === 'object' && 'bg-gray-200')}
          onClick={() => {
            setIsFixedHeight(false);
            setDataType('object');
          }}
        >
          动态行高 Object
        </button>
      </div>

      {fixedHeight ? (
        <FixedHeightVisualList />
      ) : dataType === 'array' ? (
        <DynamicVisualList />
      ) : (
        <DynamicVisualList2 />
      )}
    </div>
  );
};
