import { useEffect, useRef, useState } from 'react';
import { v4 } from 'uuid';
import { cx } from '../utils/classnames';
import { generateData } from '../utils/mock';
import { NormalType, storage } from '../algorithm/storage';

enum StorageTestType {
  dataType = 'dataType',
  expires = 'expires',
  bigData = 'bigData',
  exceedSize = 'exceedSize',
}

const buttons = [
  {
    text: '存取的各种类型数据',
    type: StorageTestType.dataType,
  },
  {
    text: '数据过期清除',
    type: StorageTestType.expires,
  },
  {
    text: '超大数据无法存入',
    type: StorageTestType.bigData,
  },
  {
    text: '空间不够删除不常用数据',
    type: StorageTestType.exceedSize,
  },
];
export const StorageTest = () => {
  const [testType, setType] = useState<StorageTestType>(StorageTestType.dataType);
  const timer = useRef<NodeJS.Timeout>();

  useEffect(() => {
    setTestType(testType);

    return () => {
      if (timer.current) {
        clearInterval(timer.current);
        clearTimeout(timer.current);
      }
    };

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  /**
   * 测试 存储的各种类型数据
   */
  const testDataType = () => {
    storage.setItem('dataType', 1);

    const data: [string, NormalType][] = [
      [v4(), true],
      [v4(), null],
      [v4(), undefined],
      [v4(), 111],
      [v4(), 'this is a sentence'],
      [v4(), generateData(2)],
    ];

    data.forEach(([key, value]) => {
      storage.setItem(key, value);
    });

    timer.current = setTimeout(() => {
      console.groupCollapsed('%c 取出各种类型数据', 'color: red;');
      data.forEach(([key]) => {
        const result = storage.getItem(key);
        console.log(result, 'type:' + typeof result);
      });
      console.groupEnd();
    }, 1000);
  };

  /**
   * 测试 数据过期进行回收
   */
  const testExpires = () => {
    storage.setLiveTime(3 * 1000); // 3s 存活
    storage.setCheckIntervalTime(5 * 1000); // 5 秒检测一次

    const data = generateData(1);
    storage.setItem('expires', data);

    timer.current = setInterval(() => {
      storage.setItem(v4(), data);
    }, 500);
  };

  /**
   * 测试 数据太大，超出储存上限
   */
  const testBigData = () => {
    const data = generateData(20000);
    storage.setItem('bigData', data);

    timer.current = setInterval(() => {
      storage.setItem(v4(), data);
    }, 2000);
  };

  /**
   * 测试 空间不够删除不常用数据
   */
  const testExceedSize = () => {
    const data = generateData(4000);
    storage.setItem('exceedSize', data);

    timer.current = setInterval(() => {
      storage.setItem(v4(), data);
    }, 1000);
  };

  const setTestType = (type: StorageTestType) => {
    setType(type);
    storage.clear();
    storage.resetTime();

    if (timer.current) {
      clearInterval(timer.current);
      clearTimeout(timer.current);
    }

    switch (type) {
      case StorageTestType.bigData:
        testBigData();
        break;
      case StorageTestType.dataType:
        testDataType();
        break;

      case StorageTestType.exceedSize:
        testExceedSize();
        break;

      case StorageTestType.expires:
        testExpires();
        break;
      default:
        break;
    }
  };

  return (
    <div>
      {buttons.map(({ text, type }) => {
        return (
          <button
            key={type}
            onClick={() => setTestType(type)}
            className={cx('mr-2', type === testType && 'bg-gray-200')}
          >
            {text}
          </button>
        );
      })}
    </div>
  );
};
