import { clamp } from 'lodash-es';
import { FC, useEffect, useRef } from 'react';
import { cx } from '../../utils/classnames';
import './scroll.css';

/**
 * 对滚动条的设置仅仅在 webkit 内核的浏览器上支持。在 Firefox 和 IE 上面不支持!!!
 * @returns
 */
export const Scrollbar = () => {
  return (
    <div>
      <h1 className='mt-4'>系统滚动条</h1>
      <div className='h-[200px] mt-4 border overflow-auto scrollbar-optimise'>
        <NumberList />
      </div>

      <h1 className='mt-4'>自定义滚动条(firefox 和 webkit内核浏览器支持)</h1>
      <CustomScroll className='mt-4'>
        <div className='h-[200px] border overflow-auto scrollbar-hidden'>
          <NumberList />
        </div>
      </CustomScroll>

      <h1 className='mt-4'>hover 显示滚动条</h1>
      <div className='h-[200px] border mt-4 overflow-auto scroll-hover-visible'>
        <NumberList />
      </div>
    </div>
  );
};

const NumberList = () => {
  return (
    <div className='w-[10000px] px-4'>
      {new Array(100).fill(1).map((_, index) => {
        return (
          <div className='h-10 border-b flex items-center' key={index}>
            {index + 1}
          </div>
        );
      })}
    </div>
  );
};

type Props = {
  className?: string;
  children: JSX.Element;
};

const CustomScroll: FC<Props> = ({ children, className }) => {
  const container = useRef<HTMLDivElement>(null);
  const scrollTrack = useRef<HTMLDivElement>(null);
  const hScrollTrack = useRef<HTMLDivElement>(null);
  const scrollThumb = useRef<HTMLDivElement>(null);
  const hScrollThumb = useRef<HTMLDivElement>(null);
  const raf = useRef(0);
  const hRaf = useRef(0);
  const mouseDownPos = useRef({
    x: 0,
    y: 0,
    top: 0,
    left: 0,
  });

  useEffect(() => {
    const scrollThumbNode = scrollThumb.current;
    const hScrollThumbNode = hScrollThumb.current;
    const scrollTrackNode = scrollTrack.current;
    const hScrollTrackNode = hScrollTrack.current;
    const containerNode = container.current?.firstElementChild as HTMLElement | null;
    if (
      !scrollThumbNode ||
      !hScrollThumbNode ||
      !scrollTrackNode ||
      !hScrollTrackNode ||
      !containerNode
    ) {
      return;
    }

    containerNode.classList.add('scrollbar-hidden');

    const handleScroll = () => {
      const hasVerticalScroll = containerNode.scrollHeight > containerNode.clientHeight;
      const hasHorizontalScroll = containerNode.scrollWidth > containerNode.clientWidth;

      console.log(hasVerticalScroll, hasHorizontalScroll);

      /**
       * 设置滚动条 track 尺寸和位置
       */
      const style = getComputedStyle(containerNode);
      const borderLeftWidth = parseInt(style.borderLeftWidth) || 0;
      const borderTopWidth = parseInt(style.borderTopWidth) || 0;
      scrollTrackNode.style.top = `${borderTopWidth}px`;
      hScrollTrackNode.style.left = `${borderLeftWidth}px`;
      if (hasVerticalScroll && hasHorizontalScroll) {
        scrollTrackNode.style.height = 'calc(100% - 12px)';
        hScrollTrackNode.style.width = 'calc(100% - 12px)';
      } else {
        scrollTrackNode.style.height = `${containerNode.clientHeight}px`;
        hScrollTrackNode.style.width = `${containerNode.clientWidth}px`;
      }

      /**
       * 设置滑块尺寸和位置
       */
      if (hasVerticalScroll) {
        const thumbHeight =
          scrollTrackNode.clientHeight *
          (scrollTrackNode.clientHeight / containerNode.scrollHeight);
        const realThumbHeight = Math.max(thumbHeight, 16);
        const hiddenHeight = containerNode.scrollHeight - containerNode.clientHeight;
        const blankTrackHeight = scrollTrackNode.clientHeight - realThumbHeight;
        const thumbTop = blankTrackHeight * (containerNode.scrollTop / hiddenHeight);

        raf.current = requestAnimationFrame(() => {
          if (raf.current) {
            cancelAnimationFrame(raf.current);
          }
          scrollThumbNode.style.display = 'block';
          scrollThumbNode.style.height = `${realThumbHeight}px`;
          scrollThumbNode.style.top = `${thumbTop}px`;
        });
      } else {
        scrollThumbNode.style.display = 'none';
      }

      if (hasHorizontalScroll) {
        const thumbWidth =
          hScrollTrackNode.clientWidth * (hScrollTrackNode.clientWidth / containerNode.scrollWidth);
        const realThumbWidth = Math.max(thumbWidth, 16);
        const hiddenWidth = containerNode.scrollWidth - containerNode.clientWidth;
        const blankTrackWidth = hScrollTrackNode.clientWidth - realThumbWidth;
        const thumbLeft = blankTrackWidth * (containerNode.scrollLeft / hiddenWidth);

        hRaf.current = requestAnimationFrame(() => {
          if (hRaf.current) {
            cancelAnimationFrame(hRaf.current);
          }
          hScrollThumbNode.style.display = 'block';
          hScrollThumbNode.style.width = `${realThumbWidth}px`;
          hScrollThumbNode.style.left = `${thumbLeft}px`;
        });
      } else {
        hScrollThumbNode.style.display = 'none';
      }
    };

    const resizeObserver = new ResizeObserver(handleScroll);

    handleScroll();
    containerNode.addEventListener('scroll', handleScroll);
    resizeObserver.observe(containerNode);
    return () => {
      containerNode.removeEventListener('scroll', handleScroll);
      resizeObserver.unobserve(containerNode);
    };
  }, []);

  const handleDrag = (event: React.MouseEvent, dir: 'x' | 'y') => {
    event.preventDefault();
    const scrollThumbNode = scrollThumb.current;
    const hScrollThumbNode = hScrollThumb.current;
    const scrollTrackNode = scrollTrack.current;
    const hScrollTrackNode = hScrollTrack.current;
    const containerNode = container.current?.firstElementChild as HTMLElement | null;
    if (
      !scrollThumbNode ||
      !hScrollThumbNode ||
      !scrollTrackNode ||
      !hScrollTrackNode ||
      !containerNode
    ) {
      return;
    }

    mouseDownPos.current = {
      x: event.clientX,
      y: event.clientY,
      top: parseInt(scrollThumbNode.style.top),
      left: parseInt(hScrollThumbNode.style.left),
    };

    const handleMouseMove = (event: MouseEvent) => {
      if (dir === 'y') {
        const hiddenHeight = containerNode.scrollHeight - containerNode.clientHeight;
        const blankTrackHeight = scrollTrackNode.clientHeight - scrollThumbNode.offsetWidth;
        const moveY = event.clientY - mouseDownPos.current.y;
        const newY = moveY + mouseDownPos.current.top;
        const top = clamp(newY, 0, blankTrackHeight);

        scrollThumbNode.style.top = `${top}px`;
        containerNode.scrollTop = hiddenHeight * (top / blankTrackHeight);
      }

      if (dir === 'x') {
        const hiddenWidth = containerNode.scrollWidth - containerNode.clientWidth;
        const blankTrackWidth = hScrollTrackNode.clientWidth - hScrollThumbNode.offsetWidth;
        const moveX = event.clientX - mouseDownPos.current.x;
        const newX = moveX + mouseDownPos.current.left;
        const left = clamp(newX, 0, blankTrackWidth);

        hScrollThumbNode.style.left = `${left}px`;
        containerNode.scrollLeft = hiddenWidth * (left / blankTrackWidth);
      }
    };

    const handleMouseUp = () => {
      document.removeEventListener('mousemove', handleMouseMove);
      document.removeEventListener('mouseup', handleMouseUp);
    };

    document.addEventListener('mousemove', handleMouseMove);
    document.addEventListener('mouseup', handleMouseUp);
  };

  return (
    <div className={cx(className, 'relative overflow-hidden')} ref={container}>
      {children}
      <div className='absolute top-0 right-0 w-3 h-full' ref={scrollTrack}>
        <div
          className='absolute w-2 bg-[#7f7f7f99] rounded-full'
          ref={scrollThumb}
          onMouseDown={(event) => handleDrag(event, 'y')}
        ></div>
      </div>
      <div className='absolute left-0 bottom-0 h-3 w-full' ref={hScrollTrack}>
        <div
          className='absolute h-2 bg-[#7f7f7f99] rounded-full'
          ref={hScrollThumb}
          onMouseDown={(event) => handleDrag(event, 'x')}
        ></div>
      </div>
    </div>
  );
};

/**
 * 滚动条固定存在的时候是会占据空间的。所以有了 overlay
 * overlay: 行为与auto相同，但滚动条绘制在内容之上而不是占用空间。 仅在基于WebKit（例如，Safari）和基于Blink的（例如，Chrome或Opera）浏览器中受支持。
 *
 * 滚动条常驻：mdn
 */
