import { FC, useEffect, useState } from 'react';
import ReactMarkdown from 'react-markdown';
import remarkGfm from 'remark-gfm';
import remarkToc from 'remark-toc';
import rehypeSlug from 'rehype-slug';
import rehypeRaw from 'rehype-raw';
import rehypeAutolinkHeadings from 'rehype-autolink-headings';
import rehypeHighlight from 'rehype-highlight';
import { loadJs } from '../utils/load-resource';
import { readTextFromBlob } from '../utils/read-text';
import { Directory } from './directory';
import 'highlight.js/styles/github.css';
import './github-markdown.css';
import './style.css';

/**
 * task 的缩进怎么回事？
 * @returns
 */

const DocsPage: FC = () => {
  const [text, setText] = useState('');
  const arr = location.pathname.split('/note/');
  const fileName = arr[arr.length - 1];
  const [fileUrl, setFileUrl] = useState(fileName ? `/${fileName}.md` : '');

  useEffect(() => {
    if (fileUrl) {
      fetchText('/docs' + fileUrl).then((text) => setText(text));
    }
  }, [fileUrl]);

  const options = {
    properties: {
      class: 'anchor',
      tabIndex: -1,
    },
    content: {
      // hast: Hypertext Abstract Syntax Tree format
      // https://github.com/rehypejs/rehype-autolink-headings#optionscontent
      // https://github.com/syntax-tree/hast#element
      // https://github.com/syntax-tree/hastscript#use
      type: 'element',
      tagName: 'span',
      properties: {
        className: ['octicon', 'octicon-link'],
      },
      children: [],
    },
  };
  return (
    <div className='flex h-screen'>
      <Directory setFileUrl={setFileUrl} />

      <div className='overflow-auto markdown-body flex-1 flex justify-center'>
        <ReactMarkdown
          className='max-w-[894px] px-5 h-max py-10'
          remarkPlugins={[remarkGfm, [remarkToc, { heading: '目录' }]]}
          rehypePlugins={[
            rehypeRaw,
            rehypeHighlight,
            rehypeSlug,
            [rehypeAutolinkHeadings, options],
          ]}
          components={{
            pre({ children }) {
              return (
                <div className='overflow-auto'>
                  <pre>{children}</pre>
                </div>
              );
            },
          }}
        >
          {text}
        </ReactMarkdown>
      </div>
    </div>
  );
};

export default DocsPage;

export const fetchText = async (fileUrl: string) => {
  await loadJs('/jschardet.js');
  return fetch(fileUrl)
    .then((response) => response.blob())
    .then(async (blob) => {
      const text = await readTextFromBlob(blob);
      return text;
    });
};
