import { useRef, useState } from 'react';

const mementos: string[] = [];

export const History = () => {
  const inputRef = useRef<HTMLInputElement>(null);
  const [value, setValue] = useState<string>('hello world');

  const handleChange = () => {
    if (!inputRef.current) return;

    mementos.push(value);
    setValue(inputRef.current.value);
  };

  const handleClick = () => {
    if (!inputRef.current) return;

    const lastMemento = mementos.pop();
    setValue(lastMemento ? lastMemento : inputRef.current.value);
  };

  return (
    <div>
      <h4> Type some characters and hit Undo </h4>
      <input value={value} ref={inputRef} onChange={handleChange} />
      <button onClick={handleClick}>Undo</button>
    </div>
  );
};

// refer: https://stackoverflow.com/questions/54416318/how-to-make-a-undo-redo-function?answertab=modifieddesc#tab-top
