import { isEqual, sortBy } from 'lodash-es';
import React, {
  createContext,
  FC,
  memo,
  useContext,
  useEffect,
  useMemo,
  useRef,
  useState,
} from 'react';
import { MdKeyboardArrowLeft, MdKeyboardArrowRight } from 'react-icons/md';
import { v4 } from 'uuid';
import { autoScrollY, rafY } from '../utils/auto-scroll';
import { calendarUtil } from '../utils/calender';
import { cx } from '../utils/classnames';
import { getDateTimeStamp, isToday } from '../utils/dates';
import { centerOfRectangle, distanceBetween } from '../utils/intersection-ratio';
import { numberToPercent } from '../utils/number';
import { OneDayMillisecond } from './timeline';

//#region context 和 interface 部分
export enum Direction {
  left = 'left',
  right = 'right',
}
export const DirectionMap = [Direction.left, Direction.right];

enum ShowType {
  SINGLE = 'SINGLE',
  MULTIPLE = 'MULTIPLE',
}
interface Todo {
  id: string;
  startTime?: number;
  endTime?: number;
  title?: number;
}

interface CalenderContext {
  showType: ShowType;
  setShowType: React.Dispatch<React.SetStateAction<ShowType>>;
  calenderDates: number[];
  calenderDatesRef: React.MutableRefObject<number[]>;
  setCalenderDates: React.Dispatch<React.SetStateAction<number[]>>;
  container: React.MutableRefObject<HTMLDivElement | null>;
  calenderHeader: React.MutableRefObject<HTMLDivElement | null>;
}

interface TodoContext {
  todoList: Todo[];
  todoListRef: React.MutableRefObject<Todo[]>;
  setTodoList: React.Dispatch<React.SetStateAction<Todo[]>>;
}
export const CalenderContext = createContext<CalenderContext | undefined>(undefined);
export const TodoContext = createContext<TodoContext | undefined>(undefined);
export const useCalender = () => {
  const context = useContext(CalenderContext);
  if (!context) {
    throw new Error('useBitable');
  }
  return context;
};
export const useTodoList = () => {
  const context = useContext(TodoContext);
  if (!context) {
    throw new Error('useBitable');
  }
  return context;
};

interface RenderTodo {
  id: string;
  rawStartTime: number;
  rawEndTime: number;
  renderStartDate: number;
  renderEndDate: number;
  weekStartDate: number;
  weekEndDate: number;
  isStartDate: boolean;
  isEndDate: boolean;
  order: number;
}

const useSelectTodo = (firstDay: number, lastDay: number) => {
  const { todoList } = useTodoList();
  const renderTodo: RenderTodo[] = [];

  todoList.forEach(({ startTime: rawStartTime, endTime: rawEndTime, id }) => {
    if (!rawStartTime || !rawEndTime) return;

    const renderStartDate = rawStartTime || rawEndTime;
    const renderEndDate = rawEndTime || rawStartTime;
    if (!renderStartDate || !renderEndDate) return;

    const startDate = getDateTimeStamp(renderStartDate);
    const endDate = getDateTimeStamp(renderEndDate);
    if (endDate < firstDay || startDate > lastDay) return;

    const weekStartDate = startDate >= firstDay && startDate <= lastDay ? startDate : firstDay;
    const weekEndDate = endDate >= firstDay && endDate <= lastDay ? endDate : lastDay;

    renderTodo.push({
      id,
      rawStartTime,
      rawEndTime,
      renderStartDate,
      renderEndDate,
      weekStartDate,
      weekEndDate,
      isStartDate: weekStartDate === startDate,
      isEndDate: weekEndDate === endDate,
      order: 0,
    });
  });

  const sortedTodo = sortBy(renderTodo, (item) => item.weekStartDate);
  const dateSet: Record<string, number[]> = {};
  sortedTodo.forEach((card) => {
    let order = 0;
    const orderList = dateSet[card.weekStartDate];
    if (orderList) {
      order = Object.entries(sortBy(orderList)).findIndex(([key, value]) => Number(key) !== value);
      order = order === -1 ? orderList.length : order;
    }

    let nextWeekDay = card.weekStartDate;
    while (nextWeekDay <= card.weekEndDate) {
      dateSet[nextWeekDay] = dateSet[nextWeekDay] ?? [];
      dateSet[nextWeekDay]?.push(order);
      nextWeekDay += OneDayMillisecond;
    }

    card.order = order;
  });

  return sortedTodo;
};

// #endregion
export const CARD_PADDING = 4;
export const CARD_GAP = 4;
export const PROPERTY_HEIGHT = 22;
export const PROPERTY_TOP = 36;

export const getCardHeight = (length = 1) => {
  return CARD_PADDING * 2 + PROPERTY_HEIGHT * length + CARD_GAP;
};

export const CalenderView: FC = () => {
  const [showType, setShowType] = useState<ShowType>(ShowType.SINGLE);
  const [calenderDates, setCalenderDates] = useState<number[]>(
    showType === ShowType.SINGLE ? getOneMonthDates() : getCalenderDates()
  );
  const calenderDatesRef = useRef<number[]>([]);
  const todoListRef = useRef<Todo[]>([]);
  const container = useRef<HTMLDivElement | null>(null);
  const calenderHeader = useRef<HTMLDivElement | null>(null);
  const [todoList, setTodoList] = useState<Todo[]>([
    {
      id: v4(),
      title: 1,
      startTime: getDateTimeStamp(Date.now()) + 3 * OneDayMillisecond,
      endTime: getDateTimeStamp(Date.now()) + 4 * OneDayMillisecond,
    },
    {
      id: v4(),
      title: 2,
      startTime: getDateTimeStamp(Date.now()) + 5 * OneDayMillisecond,
      endTime: getDateTimeStamp(Date.now()) + 6 * OneDayMillisecond,
    },
  ]);

  // useMemo 用来优化，避免 todoContextData 变换时产生新的引用引起子组件的 render
  const calenderContextData = useMemo(() => {
    return {
      container,
      showType,
      setShowType,
      calenderDates,
      setCalenderDates,
      calenderDatesRef,
      calenderHeader,
    };
  }, [showType, calenderDates]);

  const todoContextData = useMemo(() => {
    return {
      todoList,
      setTodoList,
      todoListRef,
    };
  }, [todoList, setTodoList, todoListRef]);

  todoListRef.current = todoList;
  calenderDatesRef.current = calenderDates;
  return (
    <CalenderContext.Provider value={calenderContextData}>
      <TodoContext.Provider value={todoContextData}>
        <Calender />
      </TodoContext.Provider>
    </CalenderContext.Provider>
  );
};

export const Calender = () => {
  const { setTodoList } = useTodoList();
  const { container, calenderDates, setCalenderDates, showType } = useCalender();
  const loading = useRef<boolean>(false);
  const showTypeRef = useRef(showType);
  showTypeRef.current = showType;
  const [currentMonth, setCurrentMonth] = useState<number>(getDateTimeStamp(new Date().setDate(1)));

  useEffect(() => {
    const containerNode = container.current;
    if (!containerNode) return;

    const handleScroll = () => {
      if (showTypeRef.current === ShowType.SINGLE) return;

      const containerNode = container.current;
      if (!containerNode) return;

      if (
        containerNode.scrollHeight < containerNode.scrollTop + containerNode.clientHeight + 200 &&
        !loading.current
      ) {
        loading.current = true;
        setCalenderDates((calenderDates) => {
          return calenderDates.concat(
            getCalenderDates(
              'append',
              (calenderDates[calenderDates.length - 1] as number) + OneDayMillisecond
            )
          );
        });
      }
    };

    containerNode.addEventListener('scroll', handleScroll);
    return () => containerNode.removeEventListener('scroll', handleScroll);
  }, [container, setCalenderDates, showType]);

  useEffect(() => {
    if (showType === ShowType.SINGLE) return;
    loading.current = false;

    const containerNode = container.current;
    if (!containerNode) return;
    if (containerNode.scrollHeight <= containerNode.clientHeight) {
      setCalenderDates((calenderDates) => {
        return calenderDates.concat(
          getCalenderDates(
            'append',
            (calenderDates[calenderDates.length - 1] as number) + OneDayMillisecond
          )
        );
      });
    }
  }, [calenderDates.length, container, setCalenderDates, showType]);

  return (
    <div
      className='max-h-full w-full mx-auto flex items-center flex-col overflow-auto relative'
      style={{
        background:
          'linear-gradient(90deg, #f5f5f5 14.285714285%, transparent 14.285714285%, transparent 85.714285714%, #f5f5f5 85.714285714%)',
      }}
      ref={container}
    >
      <Header currentMonth={currentMonth} setCurrentMonth={setCurrentMonth} />
      <div className='w-full border-l min-w-max'>
        {calenderDates.map((value, index) => {
          if (index % 7 === 0) {
            return (
              <Week
                key={value}
                dates={calenderDates.slice(index, index + 7)}
                setCurrentMonth={setCurrentMonth}
                setTodoList={setTodoList}
              />
            );
          }
          return null;
        })}
      </div>
    </div>
  );
};

const Header: FC<{
  currentMonth: number;
  setCurrentMonth: React.Dispatch<React.SetStateAction<number>>;
}> = ({ currentMonth, setCurrentMonth }) => {
  const { calenderHeader, setCalenderDates, container, calenderDates, showType, setShowType } =
    useCalender();

  useEffect(() => {
    const containerNode = container.current;
    if (!containerNode) return;

    highlightCurrentDate(currentMonth, calenderDates, containerNode);
  }, [calenderDates, container, currentMonth]);

  const handleJump = (target: 'pre' | 'next' | 'today') => {
    let firstDate = Date.now();
    if (target === 'today') {
      const today = new Date();
      firstDate = today.setDate(1); // 当月第一天
    } else {
      const currentMonthObj = new Date(currentMonth);
      const month = currentMonthObj.getMonth();
      const newMonth = target === 'pre' ? month - 1 : month + 1;
      firstDate = currentMonthObj.setMonth(newMonth, 1);
    }

    setCurrentMonth(firstDate);
    if (showType === ShowType.SINGLE) {
      setCalenderDates(getOneMonthDates(firstDate));
    } else {
      const containerNode = container.current;
      if (!containerNode) return;

      setCalenderDates(getCalenderDates('init', firstDate));
      containerNode.scrollTop = 0;
    }
  };

  const dateObj = new Date(currentMonth);

  return (
    <div
      className='flex flex-col sticky py-2 top-0 border w-full bg-white z-10 justify-between px-2'
      ref={calenderHeader}
    >
      <div className='flex items-center h-8'>
        <div className='h-full flex bg-white'>
          {currentMonth && (
            <span className='h-full inline-flex items-center'>
              {dateObj.getFullYear()}年{dateObj.getMonth() + 1}月
            </span>
          )}
        </div>
        <div className='ml-auto bg-white flex items-center'>
          <button
            onClick={() => {
              const firstDate = Date.now();
              setCurrentMonth(firstDate);
              setShowType(ShowType.SINGLE);
              console.log(getOneMonthDates(firstDate));

              setCalenderDates(getOneMonthDates(firstDate));
            }}
            className={cx(showType === ShowType.SINGLE && 'bg-gray-200')}
          >
            单月
          </button>
          <button
            onClick={() => {
              const firstDate = Date.now();
              setCurrentMonth(firstDate);
              setShowType(ShowType.MULTIPLE);
              setCalenderDates(getCalenderDates('init', firstDate));
            }}
            className={cx(showType === ShowType.MULTIPLE && 'bg-gray-200')}
          >
            多月
          </button>

          <button onClick={() => handleJump('pre')}>
            <MdKeyboardArrowLeft className='w-5 h-5' />
          </button>
          <button className='bg-white px-2 cursor-pointer' onClick={() => handleJump('today')}>
            今天
          </button>
          <button onClick={() => handleJump('next')}>
            <MdKeyboardArrowRight className='w-5 h-5' />
          </button>
        </div>
      </div>
      <div className='flex mt-2'>
        {'日_一_二_三_四_五_六'.split('_').map((week) => (
          <div key={week} className='flex-1 text-center'>
            周{week}
          </div>
        ))}
      </div>
    </div>
  );
};

const Week: FC<{
  dates: number[];
  setCurrentMonth: React.Dispatch<React.SetStateAction<number>>;
  setTodoList: React.Dispatch<React.SetStateAction<Todo[]>>;
}> = memo(({ dates, setCurrentMonth, setTodoList }) => {
  const { container, calenderHeader, showType } = useCalender();
  const weekRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    if (showType === ShowType.SINGLE) return;

    const containerNode = container.current;
    if (!containerNode) return;

    const handleScroll = () => {
      if (!weekRef.current || !calenderHeader.current) return;
      const weekRect = weekRef.current?.getBoundingClientRect();
      const headerRect = calenderHeader.current.getBoundingClientRect();
      if (headerRect.bottom > weekRect.top && headerRect.bottom < weekRect.bottom) {
        setCurrentMonth((oldMonth) => {
          const newMonth = dates[dates.length - 1] + OneDayMillisecond;
          if (newMonth !== oldMonth) {
            return getDateTimeStamp(new Date(newMonth).setDate(1));
          }
          return oldMonth;
        });
      }
    };

    containerNode.addEventListener('scroll', handleScroll);
    return () => containerNode.removeEventListener('scroll', handleScroll);
  }, [calenderHeader, container, dates, setCurrentMonth, showType]);

  return (
    <div className='flex w-full relative calender-week min-h-[96px]' ref={weekRef}>
      {dates.map((date) => {
        return <DateNode key={date} date={date} setTodoList={setTodoList} />;
      })}
      <WeekCards firstDay={dates[0]} lastDay={dates[dates.length - 1]} weekRef={weekRef} />
    </div>
  );
}, isEqual);

const WeekCards: FC<{
  firstDay: number;
  lastDay: number;
  weekRef: React.RefObject<HTMLDivElement>;
}> = memo(({ firstDay, lastDay, weekRef }) => {
  const renderTodo = useSelectTodo(firstDay, lastDay);

  useEffect(() => {
    const weekNodes = weekRef.current;
    if (!weekNodes) return;

    const minHight = 124;
    if (renderTodo.length === 0) {
      weekNodes.style.height = `${minHight}px`;
    } else {
      const sum: Record<string, number> = {};
      renderTodo.forEach((item) => {
        let time = item.weekStartDate;
        while (time <= item.weekEndDate) {
          sum[time] = (sum[time] || 0) + 1;
          time = time + OneDayMillisecond;
        }
      });

      const maxCardNum = Math.max(...Object.values(sum));
      const height = maxCardNum * getCardHeight() + PROPERTY_TOP;
      weekNodes.style.height = `${Math.max(height, minHight)}px`;
    }
  }, [renderTodo, weekRef]);

  if (renderTodo.length === 0) return null;

  return (
    <>
      {renderTodo.map((todo) => (
        <Card {...todo} weekFirstDay={firstDay} key={todo.id} />
      ))}
    </>
  );
});

const Card: FC<RenderTodo & { weekFirstDay: number }> = memo(
  ({
    id,
    renderStartDate,
    weekStartDate,
    weekEndDate,
    weekFirstDay,
    isStartDate,
    isEndDate,
    order,
  }) => {
    const { container, calenderDatesRef } = useCalender();
    const { todoListRef, setTodoList } = useTodoList();
    const mouseDownPos = useRef({ x: 0, y: 0 });

    const handleStretch = (event: React.MouseEvent, direction: Direction) => {
      event.preventDefault();
      event.stopPropagation();

      const containerNode = container.current;
      if (!containerNode) return;
      const isLeftDirection = direction === Direction.left;
      let targetIndex: number | undefined;
      let lastCursorPos = {
        x: event.clientX,
        y: event.clientY,
      };

      const handleMouseMove = (event: MouseEvent) => {
        lastCursorPos = {
          x: event.clientX,
          y: event.clientY,
        };

        autoScrollY({ y: event.clientY, container: containerNode });

        // 中心碰撞检测当前 hover 在哪个日期上
        updateTodo(event.clientX, event.clientY);
      };

      const handleMouseUp = () => {
        containerNode.removeEventListener('scroll', handleScroll);
        document.removeEventListener('mousemove', handleMouseMove);
        document.removeEventListener('mouseup', handleMouseUp);
        cancelAnimationFrame(rafY);
      };

      const handleScroll = () => updateTodo(lastCursorPos.x, lastCursorPos.y);

      const updateTodo = (x: number, y: number) => {
        let minDistanceToCenter = Infinity;
        const calenderTodoNodes = containerNode.querySelectorAll(
          '.calender-date'
        ) as NodeListOf<HTMLElement>;
        calenderTodoNodes.forEach((node, index) => {
          const rect = node.getBoundingClientRect();
          const distBetween = distanceBetween(centerOfRectangle(rect), { x, y });
          if (distBetween < minDistanceToCenter) {
            minDistanceToCenter = distBetween;
            targetIndex = index;
          }
        });

        // 判断条件更新 todoList
        if (typeof targetIndex === 'undefined') return;
        const newDate = calenderDatesRef.current[targetIndex];
        const currentTodo = todoListRef.current.find((todo) => todo.id === id);
        if (!currentTodo) return;

        if (
          (isLeftDirection && currentTodo.startTime !== newDate) ||
          (!isLeftDirection && currentTodo.endTime !== newDate)
        ) {
          setTodoList((todoList) => {
            return todoList.map((todo) => {
              if (todo.id === id && todo.endTime && todo.startTime) {
                return {
                  ...todo,
                  startTime: isLeftDirection && newDate <= todo.endTime ? newDate : todo.startTime,
                  endTime: !isLeftDirection && newDate >= todo.startTime ? newDate : todo.endTime,
                };
              }
              return todo;
            });
          });
        }
      };

      containerNode.addEventListener('scroll', handleScroll);
      document.addEventListener('mousemove', handleMouseMove);
      document.addEventListener('mouseup', handleMouseUp);
    };

    const handleTranslate = (event: React.MouseEvent) => {
      event.preventDefault();
      mouseDownPos.current = {
        x: event.clientX,
        y: event.clientY,
      };

      const containerNode = container.current;
      if (!containerNode) return;
      // 计算当前落点是第几个日期
      const sourceTodoNode = event.currentTarget as HTMLElement;
      const sourceTodoNodeRect = sourceTodoNode.getBoundingClientRect();
      const dateNodeWidth =
        sourceTodoNodeRect.width / (parseFloat(sourceTodoNode.style.width) / 100) / 7;
      const dragStartDate =
        renderStartDate +
        Math.floor((event.clientX - sourceTodoNodeRect.left) / dateNodeWidth) * OneDayMillisecond;
      let isMoving = false;
      let overlayContainer: HTMLDivElement | undefined;
      let targetIndex: number | undefined;
      let lastCursorPos = {
        x: event.clientX,
        y: event.clientY,
      };

      const handleMouseMove = (event: MouseEvent) => {
        lastCursorPos = {
          x: event.clientX,
          y: event.clientY,
        };

        if (
          !isMoving &&
          Math.abs(event.clientX - mouseDownPos.current.x) < 5 &&
          Math.abs(event.clientY - mouseDownPos.current.y) < 5
        ) {
          return;
        }

        if (!isMoving) {
          isMoving = true;

          overlayContainer = document.createElement('div');
          overlayContainer.style.position = 'fixed';
          overlayContainer.style.inset = '0';
          overlayContainer.style.zIndex = '100';
          const currentTodoNodes = containerNode.querySelectorAll(`[data-todo-id="${id}"]`);
          currentTodoNodes.forEach((node) => {
            const nodeRect = node.getBoundingClientRect();
            const clonedNode = node.cloneNode(true) as HTMLElement;
            clonedNode.style.position = 'absolute';
            clonedNode.style.width = `${nodeRect.width}px`;
            clonedNode.style.height = `${nodeRect.height}px`;
            clonedNode.style.top = `${nodeRect.top}px`;
            clonedNode.style.left = `${nodeRect.left}px`;
            overlayContainer?.appendChild(clonedNode);
          });
          document.body.appendChild(overlayContainer);
        }

        autoScrollY({ y: event.clientY, container: containerNode });
        updatePosition(event.clientX, event.clientY);
      };

      const updatePosition = (x: number, y: number) => {
        if (!overlayContainer) return;
        overlayContainer.style.transform = `translate(${x - mouseDownPos.current.x}px, ${
          y - mouseDownPos.current.y
        }px)`;

        // 添加背景色
        const currentHoverDate = containerNode.querySelector('.calender-date.drag-hover-highlight');
        currentHoverDate?.classList.remove('drag-hover-highlight');
        const calenderTodoNodes = containerNode.querySelectorAll(
          '.calender-date'
        ) as NodeListOf<HTMLElement>;
        Array.from(calenderTodoNodes).find((node, index) => {
          const rect = node.getBoundingClientRect();
          const isTarget = x >= rect.left && x <= rect.right && y >= rect.top && y <= rect.bottom;

          if (isTarget) {
            targetIndex = index;
            node.classList.add('drag-hover-highlight');
          }

          return isTarget;
        });
      };

      const handleMouseUp = () => {
        containerNode.removeEventListener('scroll', handleScroll);
        document.removeEventListener('mousemove', handleMouseMove);
        document.removeEventListener('mouseup', handleMouseUp);
        cancelAnimationFrame(rafY);
        overlayContainer?.remove();
        const currentHoverDate = containerNode.querySelector('.calender-date.drag-hover-highlight');
        currentHoverDate?.classList.remove('drag-hover-highlight');
        if (!isMoving) return;

        if (typeof targetIndex === 'undefined') return;
        const todo = todoListRef.current.find((todo) => todo.id === id);
        if (!todo || !todo.startTime || !todo.endTime) return;

        const newDropDate = calenderDatesRef.current[targetIndex];
        if (newDropDate === dragStartDate) return;

        const newStartDate = newDropDate - (dragStartDate - todo.startTime);
        const newEndTime = newStartDate + todo.endTime - todo.startTime;

        setTodoList((todoList) =>
          todoList.map((todo) => {
            if (todo.id === id) {
              return {
                ...todo,
                startTime: newStartDate,
                endTime: newEndTime,
              };
            }
            return todo;
          })
        );
      };

      const handleScroll = () => {
        updatePosition(lastCursorPos.x, lastCursorPos.y);
      };

      containerNode.addEventListener('scroll', handleScroll);
      document.addEventListener('mousemove', handleMouseMove);
      document.addEventListener('mouseup', handleMouseUp);
    };

    const style = {
      top: order * getCardHeight() + PROPERTY_TOP,
      left: numberToPercent((weekStartDate - weekFirstDay) / OneDayMillisecond / 7),
      width: numberToPercent(((weekEndDate - weekStartDate) / OneDayMillisecond + 1) / 7),
      height: getCardHeight(),
    };
    return (
      <div
        key={id}
        data-todo-id={id}
        className={cx('absolute cursor-pointer pb-1', {
          'pl-1': isStartDate,
          'pr-1': isEndDate,
        })}
        style={style}
        onMouseDown={handleTranslate}
      >
        <div
          className={cx('bg-green-300 border h-full relative p-1', {
            'rounded-l': isStartDate,
            'rounded-r': isEndDate,
          })}
        >
          <span>{todoListRef.current.find((todo) => todo.id === id)?.title}</span>
          {DirectionMap.map((direction) => {
            if (direction === Direction.left && isStartDate) {
              return (
                <div
                  key={direction}
                  className='absolute h-full top-0 py-0.5 w-[4px] rounded-full hover:bg-black hover:cursor-col-resize z-10 -left-0.5'
                  onMouseDown={(event) => handleStretch(event, direction)}
                />
              );
            }

            if (direction === Direction.right && isEndDate) {
              return (
                <div
                  key={direction}
                  className='absolute h-full top-0 py-0.5 w-[4px] rounded-full hover:bg-black hover:cursor-col-resize z-10 -right-0.5'
                  onMouseDown={(event) => handleStretch(event, direction)}
                />
              );
            }
          })}
        </div>
      </div>
    );
  }
);

const DateNode: FC<{ date: number; setTodoList: React.Dispatch<React.SetStateAction<Todo[]>> }> =
  memo(({ date, setTodoList }) => {
    const today = isToday(date);

    // eslint-disable-next-line react-hooks/exhaustive-deps
    const dateObj = new Date(date);
    const result = calendarUtil.solar2lunar(
      dateObj.getFullYear(),
      dateObj.getMonth() + 1,
      dateObj.getDate()
    );

    return useMemo(() => {
      return (
        <div
          className={
            'border-r border-b px-2 calender-date flex-1 text-gray-200 text-right flex justify-between group relative'
          }
        >
          <button
            className='absolute top-0.5 left-10 opacity-0 group-hover:opacity-100 cursor-pointer text-black text-sm w-5 h-5 bg-white border rounded flex items-center text-center'
            onClick={() => {
              setTodoList((todoList) => {
                return todoList.concat({
                  id: v4(),
                  title: (todoList[todoList.length - 1].title ?? 0) + 1,
                  startTime: date,
                  endTime: date,
                });
              });
            }}
          >
            +
          </button>
          <span
            className={cx(
              today &&
                'bg-red-500 font-bold inline-block w-6 text-center text-white rounded-full h-6'
            )}
          >
            {dateObj.getDate()}
          </span>
          {result !== -1 && (
            <span className={cx('ml-1 ', (result.lunarFestival || result.Term) && 'text-red-400')}>
              {result.lunarFestival || result.Term
                ? result.lunarFestival || result.Term
                : result.lDay === 1
                ? result.IMonthCn
                : result.IDayCn}
            </span>
          )}
        </div>
      );
    }, [date, dateObj, result, setTodoList, today]);
  });

export const getCalenderDates = (type: 'init' | 'append' = 'init', time?: number) => {
  let firstSunday = 0,
    lastSaturday = 0;

  if (type === 'init') {
    const date = time ? new Date(time) : new Date();
    const month = date.getMonth(); // 当天所在月
    const firstDate = date.setMonth(month, 1); // 当月第一天
    const endDate = date.setMonth(month + 2, 0); // 下个月最后一天

    const firstMonday = getDateTimeStamp(
      firstDate - ((new Date(firstDate).getDay() || 7) - 1) * OneDayMillisecond
    );
    firstSunday = firstMonday - OneDayMillisecond;
    const lastMonday = getDateTimeStamp(
      endDate - ((new Date(endDate).getDay() || 7) - 1) * OneDayMillisecond
    );
    lastSaturday = lastMonday + OneDayMillisecond * 5;
  }

  if (type === 'append' && time) {
    const startDate = new Date(time);
    const endDate = startDate.setMonth(startDate.getMonth() + 2, 0);

    firstSunday = time;
    const lastMonday = getDateTimeStamp(
      endDate - ((new Date(endDate).getDay() || 7) - 1) * OneDayMillisecond
    );
    lastSaturday = lastMonday + OneDayMillisecond * 5;
  }

  const dates = [];
  let currentDate = firstSunday;
  while (currentDate <= lastSaturday) {
    dates.push(currentDate);
    currentDate += OneDayMillisecond;
  }

  return dates;
};

export const getOneMonthDates = (time: number = Date.now()) => {
  const date = new Date(time);
  const month = date.getMonth(); // 当天所在月
  const firstDate = date.setMonth(month, 1); // 当月第一天
  const endDate = date.setMonth(month + 1, 0); // 当月最后一天
  const firstMonday = getDateTimeStamp(
    firstDate - ((new Date(firstDate).getDay() || 7) - 1) * OneDayMillisecond
  );
  const firstSunday = firstMonday - OneDayMillisecond;
  const lastMonday = getDateTimeStamp(
    endDate - ((new Date(endDate).getDay() || 7) - 1) * OneDayMillisecond
  );
  const lastSaturday = lastMonday + OneDayMillisecond * 5;

  const dates = [];
  let currentDate = firstSunday;
  while (currentDate <= lastSaturday) {
    dates.push(currentDate);
    currentDate += OneDayMillisecond;
  }

  return dates;
};

/**
 * 为当前月的日期数字改颜色，dom 操作用来优化。每个 DateNode 都监听 currentMonth 会导致性能太差
 * @param currentMonth
 * @param calenderDates
 * @param containerNode
 */
const highlightCurrentDate = (
  currentMonth: number,
  calenderDates: number[],
  containerNode: HTMLDivElement
) => {
  const newMonthFirstDate = getDateTimeStamp(new Date(currentMonth).setDate(1));
  const newMonthLastDate = getDateTimeStamp(
    new Date(currentMonth).setMonth(new Date(currentMonth).getMonth() + 1, 0)
  );
  const firstDateIndex = calenderDates.indexOf(newMonthFirstDate);
  const monthLength = (newMonthLastDate - newMonthFirstDate) / OneDayMillisecond + 1;
  const currentMonthNodes = containerNode.querySelectorAll(
    '.calender-date.current-month'
  ) as NodeListOf<HTMLElement>;
  currentMonthNodes.forEach((node) => {
    node.classList.remove('current-month');
  });
  const calenderTodoNodes = containerNode.querySelectorAll(
    '.calender-date'
  ) as NodeListOf<HTMLElement>;
  Array.from(calenderTodoNodes)
    .slice(firstDateIndex, firstDateIndex + monthLength)
    .forEach((node) => {
      node.classList.add('current-month');
    });
};

