import { useEffect } from 'react';
import { v4 } from 'uuid';
import { generateData } from '../utils/mock';

const closure = () => {
  const data = initData();
  return () => data;
};

const initData = () => {
  const list = generateData(10000);
  return list.map((item, index) => {
    return {
      id: item.id,
      bottom: (index + 1) * 90,
      height: 90,
      diffHeight: 0,
    };
  }); // 差不多 30M 内存

  return Array(10000000);
};

export const Memory = () => {
  useEffect(() => {
    // const list = generateData(10000);
    // setInterval(() => {
    //   const id = v4();
    //   localStorage.setItem(id, JSON.stringify(list));
    // }, 2000);
    // localStorage 超过存储上限后会无法存入：
    // memory.tsx:29 Uncaught DOMException: Failed to execute 'setItem' on 'Storage': Setting the value of 'c6a9917b-2743-43d4-a0c2-4de6e1e96a56' exceeded the quota.
    // 为了解决 localStorage 超过存储限制的问题，可以引入 LRU。
    // chrome 的 memory 使用：https://www.mzh.ren/javascript-objects-size.html
  }, []);

  useEffect(() => {
    setTimeout(() => {
      window.f = closure();

      console.log('%c Data generated', 'color: #f00;', window.f());

      setTimeout(() => {
        console.log('%c Data recovery', 'color: #f00;');

        // 变量置位 null，会自动回收
        window.f = null;
      }, 4000);
    }, 1000);
  }, []);
  return null;
};

