import { useEffect, useRef, useState } from 'react';
import { generateMapData } from '../../utils/mock';

const ESTIMATE_ROW_HEIGHT = 40;
export const BUFFER_SIZE = 5;

interface CachePosition {
  id: string;
  bottom: number;
  height: number;
  diffHeight: number;
}

const { idList, dataList, dataMap } = generateMapData(1000);

export const DynamicVisualList = () => {
  const [list] = useState(dataList);
  const scrollContainerRef = useRef<HTMLDivElement>(null);
  const visualContentRef = useRef<HTMLDivElement>(null);
  const actualContentRef = useRef<HTMLDivElement>(null);
  const cachedPositions = useRef<CachePosition[]>([]);
  const [range, setRange] = useState([
    0,
    Math.min(Math.ceil(window.innerHeight / ESTIMATE_ROW_HEIGHT), list.length),
  ]);

  useEffect(() => {
    list.forEach((item, index) => {
      const position = {
        id: item.id,
        bottom: (index + 1) * ESTIMATE_ROW_HEIGHT,
        height: ESTIMATE_ROW_HEIGHT,
        diffHeight: 0,
      };
      cachedPositions.current.push(position);
    });

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  useEffect(() => {
    const visualContent = visualContentRef.current;
    if (!visualContent) return;
    visualContent.style.height = `${list.length * ESTIMATE_ROW_HEIGHT}px`;

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  useEffect(() => {
    const scrollContainer = scrollContainerRef.current;
    const actualContent = actualContentRef.current;
    const visualContent = visualContentRef.current;
    if (!scrollContainer || !actualContent || !visualContent) return;

    const getStartIndex = (scrollTop: number) => {
      const positionList = cachedPositions.current;
      let start = 0;
      let end = positionList.length - 1;
      let tempIndex = 0;

      while (start <= end) {
        tempIndex = Math.floor((start + end) / 2);
        const midValue = positionList[tempIndex];

        if (midValue.bottom === scrollTop) break;
        if (midValue.bottom < scrollTop) start = tempIndex + 1;
        if (midValue.bottom > scrollTop) end = tempIndex - 1;
      }

      const target = cachedPositions.current[tempIndex];
      if (target.bottom < scrollTop) tempIndex++;

      return tempIndex;
    };

    const handleScroll = () => {
      const scrollContainerRect = scrollContainer.getBoundingClientRect();
      const visualContentRect = visualContent.getBoundingClientRect();
      const scrollTop = scrollContainerRect.top - visualContentRect.top;
      const minVisibleLength = Math.ceil(scrollContainer.clientHeight / ESTIMATE_ROW_HEIGHT);
      const currentStartIndex = getStartIndex(scrollTop);
      const newStartIndex = Math.max(currentStartIndex - BUFFER_SIZE, 0);

      setRange((range) => {
        if (range[0] === newStartIndex) return range;
        return [newStartIndex, newStartIndex + minVisibleLength + BUFFER_SIZE];
      });
    };

    scrollContainer.addEventListener('scroll', handleScroll);
    return () => {
      scrollContainer.removeEventListener('scroll', handleScroll);
    };
  }, []);

  useEffect(() => {
    const actualContent = actualContentRef.current;
    const visualContent = visualContentRef.current;
    if (!actualContent || !visualContent) return;
    const childrenNodes = actualContent.childNodes as NodeListOf<HTMLDivElement>;

    childrenNodes.forEach((node) => {
      const index = Number(node.dataset.index);
      const originHeight = cachedPositions.current[index].height;
      const realHeight = node.offsetHeight;
      const diffHeight = originHeight - realHeight; // 想象成真实的高度不及预估的高度

      if (diffHeight !== 0) {
        cachedPositions.current[index].height = realHeight;
        cachedPositions.current[index].diffHeight = diffHeight;
      }
    });

    let cumulativeDiffHeight = 0;
    const firstNode = childrenNodes[0];
    const startIndex = Number(firstNode.dataset.index);
    const cachedPositionsLen = cachedPositions.current.length;
    for (let i = startIndex; i < cachedPositionsLen; i++) {
      const item = cachedPositions.current[i];

      if (item.diffHeight !== 0) {
        cumulativeDiffHeight += item.diffHeight;
        item.diffHeight = 0;
      }

      item.bottom = item.bottom - cumulativeDiffHeight;
    }

    const height = cachedPositions.current[cachedPositionsLen - 1].bottom;
    visualContent.style.height = `${height}px`;
  }, [range]);

  const renderContent = (startIndex: number, endIndex: number) => {
    return (
      <>
        {list.slice(startIndex, endIndex).map((item, index) => {
          return (
            <div
              className='p-2 border-b'
              key={startIndex + index}
              data-index={startIndex + index}
              data-uuid={item.id}
            >
              <span className='text-[blue] font-bold'>{item.index}</span> {item.content}
            </div>
          );
        })}
      </>
    );
  };

  const startIndex = range[0];
  const endIndex = range[1];
  const transform = `translate3d(0,${
    startIndex >= 1 ? cachedPositions.current[startIndex - 1].bottom : 0
  }px,0)`;

  // return (
  //   <div ref={scrollContainerRef} className='h-[1000px] w-[600px] border relative overflow-auto'>
  //     {renderContent(0, 5)}

  //     <div className='h-[200px] border m-2 flex items-center justify-center text-[red] border-[red]'>
  //       分割线，上面是正常的文档流，下面是虚拟长列表，
  //     </div>

  //     <div ref={visualContentRef} className='w-full relative'>
  //       <div ref={actualContentRef} className='absolute top-0 w-full' style={{ transform }}>
  //         {renderContent(startIndex, endIndex)}
  //       </div>
  //     </div>
  //   </div>
  // );

  return (
    <div ref={scrollContainerRef} className='h-[800px] w-[400px] border overflow-auto'>
      <div ref={visualContentRef} className='w-full relative'>
        <div ref={actualContentRef} className='absolute top-0 w-full' style={{ transform }}>
          {renderContent(startIndex, endIndex)}
        </div>
      </div>
    </div>
  );
};

export const DynamicVisualList2 = () => {
  const [list] = useState(idList);
  const scrollContainerRef = useRef<HTMLDivElement>(null);
  const visualContentRef = useRef<HTMLDivElement>(null);
  const actualContentRef = useRef<HTMLDivElement>(null);
  const cachedPositions = useRef<Record<string, CachePosition>>({});
  const [range, setRange] = useState([
    0,
    Math.min(Math.ceil(window.innerHeight / ESTIMATE_ROW_HEIGHT), list.length),
  ]);

  const listRef = useRef<string[]>(list);
  listRef.current = list;

  // useEffect(() => {
  //   setInterval(() => {
  //     setList((list) => {
  //       const id = v4();
  //       dataMap[id] = {
  //         id,
  //         index: 5 + 1,
  //         content: faker.lorem.sentences(),
  //       };
  //       const tempList = [...list];
  //       tempList.splice(5, 0, id);

  //       return tempList;
  //     });
  //   }, 3000);

  //   // eslint-disable-next-line react-hooks/exhaustive-deps
  // }, []);

  useEffect(() => {
    const visualContent = visualContentRef.current;
    if (!visualContent) return;
    visualContent.style.height = `${list.length * ESTIMATE_ROW_HEIGHT}px`;

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  useEffect(() => {
    list.forEach((id, index) => {
      cachedPositions.current[id] = {
        id,
        bottom: (index + 1) * ESTIMATE_ROW_HEIGHT,
        height: ESTIMATE_ROW_HEIGHT,
        diffHeight: 0,
      };
    });

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  useEffect(() => {
    const actualContent = actualContentRef.current;
    const visualContent = visualContentRef.current;
    if (!actualContent || !visualContent) return;
    const childrenNodes = actualContent.childNodes as NodeListOf<HTMLDivElement>;

    childrenNodes.forEach((node) => {
      const uuid = node.dataset.uuid;
      if (!uuid) return;
      const originHeight = cachedPositions.current[uuid].height;
      const realHeight = node.offsetHeight;
      const diffHeight = originHeight - realHeight; // 想象成真实的高度不及预估的高度，所以需要减掉

      if (diffHeight !== 0) {
        cachedPositions.current[uuid].height = realHeight;
        cachedPositions.current[uuid].diffHeight = diffHeight;
      }
    });

    let cumulativeDiffHeight = 0;
    const startIndex = range[0];
    const listLength = listRef.current.length;
    for (let i = startIndex; i < listLength; i++) {
      const id = listRef.current[i];
      const item = cachedPositions.current[id];

      if (item.diffHeight !== 0) {
        cumulativeDiffHeight += item.diffHeight;
        item.diffHeight = 0;
      }

      item.bottom = item.bottom - cumulativeDiffHeight;
    }

    const lastId = listRef.current[listLength - 1];
    const lastPosition = cachedPositions.current[lastId];
    const height = lastPosition.bottom;

    visualContent.style.height = `${height}px`;
  }, [range]);

  const getStartIndex = (scrollTop: number) => {
    const positions = cachedPositions.current;
    let start = 0;
    let end = listRef.current.length - 1;
    let tempIndex = 0;
    let tempId = '';

    while (start <= end) {
      tempIndex = Math.floor((start + end) / 2);
      tempId = listRef.current[tempIndex];
      const midValue = positions[tempId];

      if (midValue.bottom === scrollTop) break;
      if (midValue.bottom < scrollTop) start = tempIndex + 1;
      if (midValue.bottom > scrollTop) end = tempIndex - 1;
    }

    tempId = listRef.current[tempIndex];
    const target = positions[tempId];
    if (target.bottom < scrollTop) tempIndex++;

    return tempIndex;
  };

  useEffect(() => {
    const scrollContainer = scrollContainerRef.current;
    const actualContent = actualContentRef.current;
    const visualContent = visualContentRef.current;
    if (!scrollContainer || !actualContent || !visualContent) return;

    const handleScroll = () => {
      const scrollContainerRect = scrollContainer.getBoundingClientRect();
      const visualContentRect = visualContent.getBoundingClientRect();
      const scrollTop = scrollContainerRect.top - visualContentRect.top;
      const minVisibleLength = Math.ceil(scrollContainer.clientHeight / ESTIMATE_ROW_HEIGHT);
      const currentStartIndex = getStartIndex(scrollTop);
      const newStartIndex = Math.max(currentStartIndex - BUFFER_SIZE, 0);

      if (range[0] === newStartIndex) return;
      setRange([newStartIndex, newStartIndex + minVisibleLength + BUFFER_SIZE]);
    };

    scrollContainer.addEventListener('scroll', handleScroll);
    return () => {
      scrollContainer.removeEventListener('scroll', handleScroll);
    };
  }, [range]);

  const renderContent = (startIndex: number, endIndex: number) => {
    return (
      <>
        {list.slice(startIndex, endIndex).map((id) => {
          const item = dataMap[id];
          return (
            <div className='p-2 border-b' key={id} data-uuid={id}>
              <span className='text-[blue] font-bold'>{item.index}</span> {item.content}
            </div>
          );
        })}
      </>
    );
  };

  const startIndex = range[0];
  const endIndex = range[1];
  const startId = list[startIndex];
  const transform = `translate3d(0,${
    startIndex >= 1 ? cachedPositions.current[startId].bottom : 0
  }px,0)`;
  return (
    <div ref={scrollContainerRef} className='h-[800px] w-[400px] border overflow-auto'>
      <div ref={visualContentRef} className='w-full relative'>
        <div ref={actualContentRef} className='absolute top-0 w-full' style={{ transform }}>
          {renderContent(startIndex, endIndex)}
        </div>
      </div>
    </div>
  );
};

/**
 * 1. 根据单行 估计高度 计算 占位内容 高度，让容器具备和普通列表一样的滚动能力
 * 2. 每次渲染后 更新占位内容的高度以及后面内容的位置：
 * 3. 真实的 scrollTop 的计算: containerRect.top - listRect.top
 * 4. 计算内容容器的平移位置：startIndex => transformY
 * 5. 根据 scrollTop 计算 startIndex: cachePosition => startIndex
 *
 * update cachePosition 和 getStartIndex
 *
 * 实测证明 10000 个节点同时渲染都不会卡，如果会卡，那肯定不是 dom 节点的原因，一定是别的原因。
 */

