import dayjs from 'dayjs';
import { useEffect } from 'react';

const DATE_FORMAT = 'YYYY/MM/DD';
const formatYear = (year: number) => {
  if (year < 10) {
    return Number('200'.concat(String(year)));
  }
  if (year < 100 && year >= 10) {
    return Number('20'.concat(String(year)));
  }
  return year;
};

export const formatDateString = (value: string, format = DATE_FORMAT): string | undefined => {
  if (format === DATE_FORMAT) {
    const dateArr = value.split('/').map((value) => parseInt(value, 10));
    const yearString = dateArr[0] as number;
    const monthString = dateArr[1] as number;
    const dayString = dateArr[2] && isNaN(dateArr[2]) ? 1 : dateArr[2];

    if (dayString || dayString === 0) {
      let newDate = new Date();
      let day = dayString;
      let month = monthString;

      if (yearString <= 12) {
        newDate = new Date(formatYear(dayString), yearString - 1, monthString);
        day = monthString;
        month = yearString - 1;
      }

      if (yearString > 12 && yearString <= 31) {
        newDate = new Date(formatYear(dayString), monthString - 1, yearString);
        day = yearString;
        month = monthString - 1;
      }

      if (yearString > 31) {
        newDate = new Date(formatYear(yearString), monthString - 1, dayString);
        month = monthString - 1;
      }

      if (isNaN(newDate.getTime())) return;
      if (newDate.getDate() !== day || newDate.getMonth() !== month) return;
      return dayjs(newDate).format(format);
    }
  }
};

export const isValidTimeString = (value: string) => {
  return !isNaN(new Date(dayjs().format(DATE_FORMAT).concat(' ', value)).getTime());
};

