import { ReactDemo } from '../example/react-demo';
// import { Editor } from './editor';
import { PdfPreview } from './pdf';
import { StorageTest } from './storage-test';

const Example = () => {
  const [show, setShow] = useState(false);

  return (
    <div className='relative h-screen overflow-hidden'>
      <div className='h-full w-20 absolute z-50 border-r'>
        <button
          className='px-2 text-sm border-none rounded-md w-full h-10'
          onClick={() => setShow(!show)}
        />

        {show && (
          <div className='flex flex-col pt-2'>
            {Routes.map((route) => {
              return (
                <Link
                  to={`/example/${route.path}`}
                  className='w-full px-2 h-8 flex items-center mb-2 hover:bg-gray-100'
                  key={route.path}
                >
                  {route.path}
                </Link>
              );
            })}
          </div>
        )}
      </div>

      <div className='overflow-scroll h-screen m-4 ml-24'>
        <Switch>
          {Routes.map((route) => (
            <Route path={`/example/${route.path}`} component={route.component} key={route.path} />
          ))}
          <Route path='/' component={Routes[0].component} />
        </Switch>
      </div>
    </div>
  );
};

export default Example;

